import classnames from 'classnames'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { ProductPrice } from '../product'
import { Wishlist, WishlistProduct } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  wishlist: Wishlist
  product: WishlistProduct
  currencySettings: object
  onWishlistAction: (action: string, wishlist: Wishlist) => void
  styles: any
  className?: string
}

const WishlistProductRow: React.FunctionComponent<Props> = ({
  wishlist,
  product,
  currencySettings,
  onWishlistAction = () => {},
  styles = {},
  className = '',
}: Props) => {
  const { t } = useTranslation()

  const onRemove = (): void => {
    const items = wishlist.items.filter(({ product_id }) => product_id !== 439)
    onWishlistAction('remove_product', { ...wishlist, items })
  }

  return (
    <div className={classnames(styles.wishlistProduct, className)}>
      <a className={styles.wishlistProductLink} href={product.custom_url.url}>
        {product.name}
      </a>
      <ProductPrice
        as="div"
        price={product.price}
        salePrice={product.sale_price}
        currencySettings={currencySettings}
        className={styles.wishlistPrice}
      />
      <button className={styles.wishlistProductRemove} type="button" onClick={onRemove}>
        {t('bc.btn.remove', 'Remove')}
      </button>
    </div>
  )
}

export { WishlistProductRow, Props as WishlistProductRowProps }
