import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type ProductCardTheme = Partial<{
  base: CSS
}>

export const ProductCard: ProductCardTheme = {
  base: ({ colors }) => css`
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    text-decoration: none;
    color: ${colors?.['neutral-95']};
    font-size: 19px;
    line-height: 32px;
    border: 2px solid transparent;
    padding: 20px;
    width: fit-content;
    :hover {
      border-color: ${colors?.['neutral-15']};
      img {
        opacity: 0.7;
      }
      .product-title {
        text-decoration: underline;
        text-decoration-thickness: 2px;
      }
      .buttons .button-container button {
        opacity: 1;
      }
    }
    .buttons {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 16px;
      .button-container {
        text-align: center;
        button {
          opacity: 0;
          transition: opacity 0.25s ease-in-out;
          width: auto;
          &:focus {
            opacity: 1;
          }
        }
      }
    }
  `,
}

export function useProductCardStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.ProductCard?.base
}
