/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Role, RoleProps } from 'reakit/Role'

import { useWishlistDetailStyle } from '../../../theme/wishlist-detail'
import { Wishlist } from './types'

interface Props extends RoleProps {
  wishlist: Wishlist
  grid?: boolean
  children: any
}

const WishlistDetail: React.FunctionComponent<Props> = ({
  wishlist,
  grid,
  children,
  ...rest
}: Props) => {
  const css = useWishlistDetailStyle()

  return (
    <Role {...rest}>
      <h1 css={css.Header} dangerouslySetInnerHTML={{ __html: wishlist.name }} />
      <div css={grid ? css.ProductGrid : css.ProductList}>{children}</div>
    </Role>
  )
}

export { WishlistDetail, Props as WishlistDetailProps }
