/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'

import { usePricingStyle } from '@theme/pricing'

import { ProductPrice, ProductPriceProps } from '../core/product/price'

export type PricingProps = React.HTMLProps<HTMLDivElement> & {
  items: {
    id?: string | number
    label: string
    price?: ProductPriceProps
    value?: React.ReactNode
  }[]
  total: {
    label: string
    price: ProductPriceProps
  }
}

export function Pricing(props: PricingProps): React.ReactElement {
  const { items, total, ...rest } = props
  const styles = usePricingStyle()
  return (
    <div css={styles.Container} {...rest}>
      {items.map(item => (
        <div css={styles.Row} key={item.id || item.label}>
          <span>{item.label}</span>
          {item.price ? <ProductPrice as="span" css={styles.Value} {...item.price} /> : item.value}
        </div>
      ))}
      <div css={styles['Row--Total']}>
        <span>{total.label}</span>
        <ProductPrice as="span" css={styles['Value--Total']} {...total.price} />
      </div>
    </div>
  )
}
