/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Button } from 'reakit/Button'
import { Role, RoleProps } from 'reakit/Role'

import {
  useSideMenuItemStyle,
  useSideMenuLevelStyle,
  useSideMenuStyle,
} from '../../../theme/side-menu'

export type SideMenuProps<T = RoleProps> = T & {
  as?: React.ComponentType<T> | React.ElementType
}
export type SideMenuLevelProps<T = RoleProps> = T & {
  as?: React.ComponentType<T> | React.ElementType
  title: React.ReactNode
}
export type SideMenuItemProps<T = RoleProps> = T & {
  as?: React.ComponentType<T> | React.ElementType
  active?: boolean
}

export function SideMenu(props: SideMenuProps): React.ReactElement {
  const { as = 'div', ...rest } = props
  const css = useSideMenuStyle()
  return <Role role="menu" as={as} css={css} {...rest} />
}

function SideMenuLevel(props: SideMenuLevelProps): React.ReactElement {
  const { as = 'ul', title, children, ...rest } = props
  const css = useSideMenuLevelStyle()
  return (
    <Role as={as} css={css} {...rest}>
      <div className="SideMenuLevel__title">{title}</div>
      {children}
    </Role>
  )
}

function SideMenuItem(props: SideMenuItemProps): React.ReactElement {
  const { as = Button, active, ...rest } = props
  const css = useSideMenuItemStyle(active ? 'active' : undefined)

  return (
    <li css={css}>
      <Role as={as} {...rest} />
    </li>
  )
}

SideMenu.Level = SideMenuLevel
SideMenu.Item = SideMenuItem
