import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

type PricingComponents = 'Container' | 'Row' | 'Row--Total' | 'Value' | 'Value--Total'

export type PricingTheme = Partial<Record<PricingComponents, { base: CSS }>>

export const Pricing: PricingTheme = {
  Container: {
    base: ({ colors }) => css`
      background: ${colors['neutral-10'] as CSSPrimitive};
      padding: 24px 32px;
    `,
  },
  Row: {
    base: ({ typography }) => css`
      ${typography['body-small'] as CSSPrimitive}
      display: flex;
      margin-bottom: 32px;
      align-items: center;
      justify-content: space-between;
    `,
  },
  Value: {
    base: ({ typography }) => css`
      ${typography.body as CSSPrimitive}
      width: auto;
    `,
  },
  'Row--Total': {
    base: ({ typography, colors }) => css`
      ${typography['body-large'] as CSSPrimitive}
      display: flex;
      position: relative;
      padding-top: 32px;

      &:before {
        content: '';
        width: calc(100% + 64px);
        height: 2px;
        display: block;
        background: ${colors['neutral-0']};
        position: absolute;
        top: 0;
        left: -32px;
      }
    `,
  },
  'Value--Total': {
    base: ({ typography }) =>
      css`
        ${typography['display-small'] as CSSPrimitive}
        font-weight: bold;
        width: auto;
        margin-left: auto;
      `,
  },
}

export function usePricingStyle(): Partial<Record<PricingComponents, CSS>> {
  const theme = useTheme()
  return Object.entries(theme?.components?.Pricing).reduce(
    (
      acc: Partial<Record<PricingComponents, CSS>>,
      [key, value]: [PricingComponents, { base: CSS }]
    ) => {
      return {
        ...acc,
        [key]: value?.base,
      }
    },
    {}
  )
}
