import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type ProductReviewTheme = Partial<{
  base: CSS
}>

export const ProductReview: ProductReviewTheme = {
  base: ({ colors, typography }) => css`
    display: block;
    margin-top: 2em;
    .ProductReview__Author {
      ${typography.overline as CSSPrimitive}
      text-transform: uppercase;
      display: inline-block;
      @media (min-width: 375px) {
        ::after {
          content: '/';
          padding: 0 16px;
        }
      }
    }

    .ProductReview__Date {
      ${typography['body-small'] as CSSPrimitive}
      color: ${colors['neutral-55']};
      @media (min-width: 375px) {
        display: inline-block;
      }
    }
    .ProductReview__Rating {
      margin: 0;
    }
    .ProductReview__Title {
      ${typography['body-bold'] as CSSPrimitive}
      margin: 4px 0;
    }
    .ProductReview__Text {
      ${typography['body-small'] as CSSPrimitive}

    }
  `,
}

export function useProductReviewStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.ProductReview?.base
}
