import * as React from 'react'
import { Role } from 'reakit/Role'

import { useProductTitleStyle } from '../../../../theme/product-title'

type Props<T = React.HTMLProps<HTMLElement>> = T & {
  text: string
  as?: React.ComponentType<T> | React.ElementType
}

const ProductTitle: React.FunctionComponent<Props> = (props: Props) => {
  const { as = 'h2', text, ...rest } = props
  // TODO: Deprecate props.tag

  const css = useProductTitleStyle()

  return (
    <Role as={as} css={css} {...rest}>
      {text}
    </Role>
  )
}

export { ProductTitle }
