/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Role, RoleProps } from 'reakit/Role'

import { useProductReviewStyle } from '../../../../theme/product-review'
import { StarRating } from '../../../utilities/star-rating'
import { ReviewForm } from './form'

type Review = {
  author?: string
  date: Date
  rating?: number
  title: string
  text?: string
}

export type ProductReviewProps<T = RoleProps> = T & {
  review: Review
  as?: React.ComponentType<T> | React.ElementType
}

function getDate(date: Date): string {
  return new Intl.DateTimeFormat('en-US', {
    month: 'long',
    day: 'numeric',
    year: 'numeric',
  }).format(date)
}

function ProductReview(props: ProductReviewProps): React.ReactElement {
  const { review, as = 'div', ...rest } = props

  const css = useProductReviewStyle()

  return (
    <Role css={css} as={as} {...rest}>
      {review.author && <p className="ProductReview__Author">{review.author}</p>}
      <p className="ProductReview__Date">{getDate(new Date(review.date))}</p>
      {(review.rating || review.rating === 0) && (
        <StarRating rating={review.rating} className="ProductReview__Rating" />
      )}
      <h4 className="ProductReview__Title">{review.title}</h4>
      <p className="ProductReview__Text">{review.text}</p>
    </Role>
  )
}

export { ProductReview, ReviewForm }
