import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type FieldVariants = 'inline' | 'block'

export type ProfileFormTheme = Partial<{
  Form: CSS
  Heading: CSS
  Fieldset: CSS
  Field: (variant: FieldVariants) => CSS
  Input: CSS
  Label: CSS
  LabelOptional: CSS
}>

export const ProfileForm: ProfileFormTheme = {
  Form: css`
    padding: 8px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  `,
  Heading: ({ typography, colors }) => css`
    ${typography.overline as CSSPrimitive}
    color: ${colors['neutral-55']};
    margin-bottom: 24px;
    padding: 0 20px;
  `,
  Fieldset: css`
    margin-bottom: 48px;
    width: 100%;
    margin-left: -20px;
    margin-right: -20px;
    max-width: 800px;
  `,
  Field: variant => () => css`
    margin: 0 20px 32px;

    @media (min-width: 960px) {
      width: calc(50% - 80px);
      display: ${variant === 'inline' ? 'inline-block' : 'block'};
    }
  `,
  Input: ({ colors }) => css`
    background: ${colors['neutral-0']};
    border: 2px solid ${colors['neutral-15']};
    box-sizing: border-box;
    border-radius: 24px;
    width: 100%;
    padding: 8px 20px;
  `,
  Label: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive};
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
  `,
  LabelOptional: ({ typography, colors }) => css`
    ${typography['body-small'] as CSSPrimitive};
    color: ${colors['neutral-55']};
  `,
}

export function useProfileFormStyle(): ProfileFormTheme {
  const theme = useTheme()
  return theme?.components?.ProfileForm
}
