/** @jsx jsx */
import { jsx } from '@emotion/react'
import format from 'date-fns/format'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'
import { StatusVariants, useOrderDetailStyle } from '@theme/order-detail'

import { ProductPrice } from '../product'
import { Order } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  order: Order
  statusVariant?: StatusVariants
}

const OrderDetail: React.FunctionComponent<Props> = ({
  order,
  statusVariant,
  className,
}: Props) => {
  const { t } = useTranslation()
  const styles = useOrderDetailStyle()

  return (
    <div className={className}>
      <div css={styles.OrderDetails}>
        <div css={styles.DetailRow}>
          <span>{t('bc.order.status', 'Status')}</span>
          <span css={styles.OrderStatus(statusVariant)}>{order.custom_status}</span>
        </div>
        <div css={styles.DetailRow}>
          <span>{t('bc.order.ordered', 'Ordered')}</span>
          <span css={styles.DetailValue}>
            {format(new Date(order.date_created), 'MMM dd, yyyy')}
          </span>
        </div>
        <div css={styles.DetailRow}>
          <span>{t('bc.order.last_udpate', 'Last Update')}</span>
          <span css={styles.DetailValue}>
            {format(new Date(order.date_modified), 'MMM dd, yyyy')}
          </span>
        </div>
        <div css={styles.DetailRow}>
          <span>{t('bc.order.payment_method', 'Payment Method')}</span>
          <span css={styles.DetailValue}>{order.payment_method}</span>
        </div>
      </div>
      <div css={styles.Pricing}>
        <div css={styles.DetailRow}>
          <span>{t('bc.order.items_subtotal', 'Items Subtotal')}</span>
          <ProductPrice
            as="span"
            price={order.subtotal_ex_tax}
            salePrice={0}
            currencySettings={{ currency: order.currency_code }}
            css={styles.DetailValue}
          />
        </div>
        {Number(order.store_credit_amount) > 0 && (
          <div css={styles.DetailRow}>
            <span>{t('bc.order.store_credit', 'Store Credit Applied')}</span>
            <ProductPrice
              as="span"
              price={order.store_credit_amount * -1}
              salePrice={0}
              currencySettings={{ currency: order.currency_code }}
              css={styles.DetailValue}
            />
          </div>
        )}
        {Number(order.coupon_discount) > 0 && (
          <div css={styles.DetailRow}>
            <span>
              {t('bc.order.promo_code', 'Promo Code')}
              {
                // TODO: Get Promo Code Name
              }
            </span>
            <ProductPrice
              as="span"
              price={order.coupon_discount * -1}
              salePrice={0}
              currencySettings={{ currency: order.currency_code }}
              css={styles.DetailValue}
            />
          </div>
        )}
        {Number(order.gift_certificate_amount) > 0 && (
          <div css={styles.DetailRow}>
            <span>{t('bc.order.gift_certificate', 'Gift certificate applied')}</span>
            <ProductPrice
              as="span"
              price={order.gift_certificate_amount * -1}
              salePrice={0}
              currencySettings={{ currency: order.currency_code }}
              css={styles.DetailValue}
            />
          </div>
        )}
        <div css={styles.DetailRow}>
          <span>{t('bc.order.tax', 'Estimated tax to be collected')}</span>
          <ProductPrice
            as="span"
            price={order.subtotal_tax}
            salePrice={0}
            currencySettings={{ currency: order.currency_code }}
            css={styles.DetailValue}
          />
        </div>
        <div css={styles.PriceRowTotal}>
          <span>
            {t('bc.order.total', 'Total')} ({order.currency_code})
          </span>
          <ProductPrice
            as="span"
            price={parseFloat(order.total_inc_tax)}
            salePrice={0}
            currencySettings={{ currency: order.currency_code }}
            css={styles.PriceValueTotal}
          />
        </div>
      </div>
    </div>
  )
}

export { OrderDetail, Props as OrderDetailProps }
