/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'

import { useProductImageStyle } from '../../../../theme/product-image'

type Props = React.DetailedHTMLProps<React.ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement>

const ProductImage: React.FunctionComponent<Props> = (props: Props) => {
  const { src, alt = '', children, ...rest } = props
  const css = useProductImageStyle()

  // TODO: Support Gatsby Image, NextJS Image, etc...
  return (
    <div css={css}>
      <img src={src} alt={alt} {...rest} />
      {children}
    </div>
  )
}

export { ProductImage }
