import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type NotificationVariant = 'success' | 'info' | 'error' | 'warning'

export type NotificationTheme = Partial<{
  base: CSS
  variants: Record<NotificationVariant, CSS>
}>

export const Notification: NotificationTheme = {
  base: css`
    padding: 16px;
  `,
  variants: {
    success: ({ colors, components }) =>
      css`
        ${components.Notification.base as CSSPrimitive}
        background: ${colors.success};
        color: ${colors?.['neutral-0']};
      `,
    error: ({ colors, components }) =>
      css`
        ${components.Notification.base as CSSPrimitive}
        background: ${colors.danger};
        color: ${colors?.['neutral-0']};
      `,
    info: ({ colors, components }) =>
      css`
        ${components.Notification.base as CSSPrimitive}
        background: ${colors.info};
        color: ${colors?.['neutral-0']};
      `,
    warning: ({ colors, components }) =>
      css`
        ${components.Notification.base as CSSPrimitive}
        background: ${colors.warning};
        color: ${colors?.['neutral-95']};
      `,
  },
}

export function useNotificationStyle(variant: NotificationVariant): CSS {
  const theme = useTheme()
  const styles = theme?.components?.Notification?.variants?.[variant]
  return styles
}
