import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type StatusVariants = 'info' | 'warning' | 'success' | 'attention' | 'danger'

export type OrderDetailTheme = {
  Pricing: CSS
  DetailRow: CSS
  DetailValue: CSS
  PriceRowTotal: CSS
  PriceValueTotal: CSS
  OrderDetails: CSS
  OrderStatus: (variant?: StatusVariants) => CSS
}

export const OrderDetail: OrderDetailTheme = {
  Pricing: ({ colors }) => css`
    background: ${colors['neutral-10'] as CSSPrimitive};
    padding: 36px 32px;
  `,
  DetailRow: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    display: flex;
    margin-bottom: 32px;
    align-items: center;
    justify-content: space-between;
  `,
  DetailValue: ({ typography }) => css`
    ${typography.body as CSSPrimitive}
    width: auto;
  `,
  PriceRowTotal: ({ typography, colors }) => css`
    ${typography['body-large'] as CSSPrimitive}
    display: flex;
    position: relative;
    padding-top: 32px;

    &:before {
      content: '';
      width: calc(100% + 64px);
      height: 2px;
      display: block;
      background: ${colors['neutral-0']};
      position: absolute;
      top: 0;
      left: -32px;
    }
  `,
  PriceValueTotal: ({ typography }) =>
    css`
      ${typography['display-small'] as CSSPrimitive}
      font-weight: bold;
      width: auto;
      margin-left: auto;
    `,
  OrderDetails: () => css`
    padding: 0 32px;
    margin-top: 32px;

    @media (min-width: 960px) {
      margin-top: 0;
    }
  `,
  OrderStatus: (colorVariant = 'info') => ({ colors, typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    background: ${colors['neutral-10']};
    padding: 2px 16px 2px 32px;
    position: relative;
    border-radius: 46px;
    white-space: nowrap;
    margin-left: 10px;

    &:before {
      content: '';
      border-radius: 100%;
      width: 8px;
      height: 8px;
      top: 10px;
      position: absolute;
      display: block;
      left: 16px;
      background: ${colors[colorVariant]};
    }
  `,
}

export function useOrderDetailStyle(): OrderDetailTheme {
  const theme = useTheme()
  return theme?.components?.OrderDetail
}
