import classnames from 'classnames'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { QuantitySelector } from '../../../utilities'
import { ProductImage, ProductPrice, ProductTitle } from '../../product'
import { CartItem, CartType } from '../types'

interface Props extends React.HTMLProps<HTMLElement> {
  cart: CartType
  item: CartItem
  styles?: any
  onUpdate: (method: string, id: string, payload: object) => void
  className?: string
}

const Item: React.FunctionComponent<Props> = ({
  item,
  cart,
  styles = {},
  onUpdate,
  className = '',
}: Props) => {
  const { t } = useTranslation()

  function updateItemQuantity(quantity: number): void {
    const newItem = {
      quantity,
      productId: item.productId,
      variantId: item.variantId,
    }
    const method = quantity === 0 ? 'delete' : 'put'
    onUpdate(method, item.id, newItem)
  }

  const onChangeQuantity = (quantity: number): void => updateItemQuantity(quantity)
  const onRemove = (): void => updateItemQuantity(0)

  return (
    <div className={classnames(styles.cartItem, className)}>
      <a href={item.url}>
        <ProductImage src={item.imageUrl} />
      </a>
      <a href={item.url}>
        <ProductTitle text={item.name} as="h3" />
      </a>
      <QuantitySelector onChangeQuantity={onChangeQuantity} defaultQuantity={item.quantity} />
      <div className={styles.cartItemPrices}>
        <ProductPrice
          price={item.salePrice}
          salePrice={0}
          currencySettings={{ currency: cart.currency.code }}
        />{' '}
        each
        <div>
          <ProductPrice
            price={item.extendedSalePrice}
            className={styles.cartItemTotal}
            salePrice={0}
            currencySettings={{ currency: cart.currency.code }}
          />
        </div>
        <div>
          <button type="button" onClick={onRemove}>
            {t('Remove')}
          </button>
        </div>
      </div>
    </div>
  )
}

export { Item, Props as ItemProps }
