import classnames from 'classnames'
import React from 'react'

import { Wishlist } from './types'
import { WishlistRow } from './wishlist-row'

interface Props extends React.HTMLProps<HTMLElement> {
  wishlists: Wishlist[]
  WishlistComponent?: any
  styles: any
  onWishlistAction: (action: string, wishlist: Wishlist) => void
  className?: string
}

const WishlistList: React.FunctionComponent<Props> = ({
  wishlists = [],
  styles = {},
  WishlistComponent = WishlistRow,
  onWishlistAction = () => {},
  className = '',
}: Props) => {
  return (
    <div className={classnames(styles.wishlists, className)}>
      <div className={styles.wishlist}>
        <div className={styles.wishlistInfo}>
          <span className={styles.wishlistName}>Wishlist Name</span>
          <span className={styles.wishlistItems}>Items</span>
          <span className={styles.wishlistShared}>Shared</span>
        </div>
        <span className={styles.wishlistActions} />
      </div>
      {wishlists.map(wishlist => (
        <WishlistComponent
          onWishlistAction={onWishlistAction}
          key={wishlist.id}
          wishlist={wishlist}
          styles={styles}
        />
      ))}
    </div>
  )
}

export { WishlistList, Props as WishlistListProps }
