/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Button as ReakitButton, ButtonProps as ReakitButtonProps } from 'reakit'

import { ButtonVariant, useButtonStyle } from '../../../theme/button'

export type ButtonProps = ReakitButtonProps & {
  variant?: ButtonVariant
  text?: string
}

export function Button(props: ButtonProps): React.ReactElement {
  const { variant = 'primary', text, children } = props
  const css = useButtonStyle(variant)

  return (
    <ReakitButton {...props} css={css}>
      {text || children}
    </ReakitButton>
  )
}
