/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Button } from 'reakit/Button'
import { Role, RoleProps } from 'reakit/Role'
import { VisuallyHidden } from 'reakit/VisuallyHidden'

import { useProductRowStyle } from '../../../theme/product-row'
import { OptionType } from '../../core/product/options'
import { ProductPrice, ProductPriceProps } from '../../core/product/price'
import { QuantityProps, QuantitySelector } from '../../utilities/quantity-selector'

export type ProductRowProps = RoleProps & {
  image?: React.DetailedHTMLProps<React.ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement> // TODO: Support gatsby/next image
  name: string
  variant?: string
  prices: ProductPriceProps
  quantity?: QuantityProps
  options?: OptionType[]
  onDelete?: React.MouseEventHandler<HTMLButtonElement>
  editable?: boolean
}

export function ProductRow(props: ProductRowProps): React.ReactElement {
  const {
    image,
    name,
    variant,
    editable = true,
    prices,
    quantity,
    options,
    onDelete = () => {},
    ...rest
  } = props
  const styles = useProductRowStyle()

  const displayedQuantity = quantity?.quantity || quantity?.defaultQuantity

  const optionLast = options ? options.length - 1 : 0

  return (
    <Role css={styles.Container} {...rest}>
      {image?.src && <img alt={image?.alt || name} {...image} css={styles.Image} />}
      <div css={styles.Content}>
        <div>
          <p>{name}</p>
          {variant && <p css={styles.Variant}>{variant}</p>}
          {editable && quantity && <QuantitySelector {...quantity} css={styles.Quantity} />}
          {options && (
            <div css={styles.Options}>
              {options.map((option, i) => (
                <span key={option?.option_id}>
                  {option?.display_value}
                  {i !== optionLast ? <React.Fragment>, </React.Fragment> : null}
                </span>
              ))}
            </div>
          )}
        </div>
        <div>
          <div css={styles.Price}>
            {displayedQuantity && displayedQuantity > 1 ? (
              <span css={styles.PriceQuantity}>{displayedQuantity} x</span>
            ) : null}
            <ProductPrice {...prices} />
          </div>
          {editable && !!onDelete && (
            <Button css={styles.Delete} onClick={onDelete}>
              <VisuallyHidden>Delete</VisuallyHidden>
              <svg width={24} height={24} viewBox="0 0 24 24" fill="none">
                <path
                  d="M20 9l-.867 12.142A2 2 0 0117.138 23H6.862a2 2 0 01-1.995-1.858L4 9M1 5h22M8 5V2a1 1 0 011-1h6a1 1 0 011 1v3"
                  strokeWidth={2}
                  strokeMiterlimit={10}
                  strokeLinecap="square"
                />
              </svg>
            </Button>
          )}
        </div>
      </div>
    </Role>
  )
}
