/**
 * @function CreateWishlist
 *
 *
 */
import classnames from 'classnames'
import * as React from 'react'

import { useTranslation } from '@hooks/use-translation'

interface Props extends React.HTMLProps<HTMLElement> {
  onSubmit: (value: object) => boolean
  action: string
  styles: any
  className?: string
}

const CreateWishlist: React.FunctionComponent<Props> = ({
  onSubmit,
  action,
  styles = {},
  className = '',
}: Props) => {
  const { t } = useTranslation()
  const submit = (e): boolean => {
    e.preventDefault()
    const formData = new FormData(e.target)
    const data = {}
    formData.forEach((value, key) => {
      data[key] = value
    })
    return onSubmit({ ...e, data })
  }

  return (
    <form
      name="wishlist-form"
      className={classnames(styles.wishlistForm, className)}
      action={action}
      method="POST"
      encType="multipart/form-data"
      onSubmit={submit}
    >
      <div className={styles.fieldWrapper}>
        <label htmlFor="wishlist-name" key="name-label" className={styles.nameLabel}>
          {t('bc.wishlist.name', 'Wishlist Name')}
        </label>
        <input
          name="name"
          id="wishlist-name"
          placeholder="Wishlist Name"
          className={styles.name}
          type="text"
        />
      </div>
      <div className={styles.fieldWrapper}>
        <input
          name="is_public"
          id="wishlist-public"
          className={styles.public}
          type="checkbox"
          value="true"
        />
        <label className={styles.publicLabel} htmlFor="wishlist-public">
          {t('bc.wishlist.make_label', 'Make this Wish List shareable with a public link?')}
        </label>
      </div>
      <button className={styles.saveButton} type="submit">
        {t('bc.wishlist.save', 'Save Wishlist')}
      </button>
    </form>
  )
}

export { CreateWishlist, Props as CreateWishlistProps }
