import _ from 'lodash'

function convertToStorefront(obj): object {
  let camelCaseObject = _.cloneDeep(obj)

  if (_.isArray(camelCaseObject)) {
    return _.map(camelCaseObject, convertToStorefront)
  }

  camelCaseObject = _.mapKeys(camelCaseObject, (_value, key) => {
    return _.camelCase(key)
  })

  // Recursively apply throughout object
  return _.mapValues(camelCaseObject, value => {
    if (_.isPlainObject(value)) {
      return convertToStorefront(value)
    }
    if (_.isArray(value)) {
      return _.map(value, convertToStorefront)
    }
    return value
  })
}

function convertToServer(obj): object {
  let snakeCaseObject = _.cloneDeep(obj)

  if (_.isArray(snakeCaseObject)) {
    return _.map(snakeCaseObject, convertToServer)
  }

  snakeCaseObject = _.mapKeys(snakeCaseObject, (_value, key) => {
    return _.snakeCase(key)
  })

  // Recursively apply throughout object
  return _.mapValues(snakeCaseObject, value => {
    if (_.isPlainObject(value)) {
      return convertToServer(value)
    }
    if (_.isArray(value)) {
      return _.map(value, convertToServer)
    }
    return value
  })
}

function cartHasItems(cart): boolean {
  if (!cart || !cart.lineItems) {
    return false
  }

  const { physicalItems, digitalItems, customItems, giftCertificates } = cart.lineItems
  return [...physicalItems, ...digitalItems, ...customItems, ...giftCertificates].length > 0
}

export { convertToStorefront, convertToServer, cartHasItems }
