import classnames from 'classnames'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { ProductPrice } from '../../product/price'
import { CartType } from '../types'

interface Props extends React.HTMLProps<HTMLElement> {
  cart: CartType
  styles?: any
  className?: string
}

const Subtotal: React.FunctionComponent<Props> = ({ cart, styles = {}, className = '' }: Props) => {
  const { t } = useTranslation()

  return (
    <div className={classnames(styles.cartSubtotal, className)}>
      {t('bc.cart.subtotal', 'Subtotal')}:
      <ProductPrice
        price={cart.cartAmount}
        className={styles.cartSubtotalText}
        salePrice={0}
        currencySettings={{ currency: cart.currency.code }}
      />
    </div>
  )
}

export { Subtotal, Props as SubtotalProps }
