/**
 * @function NewsletterForm
 */

import React, { useState } from 'react'

import { NewsletterEmail, NewsletterEmailProps } from './email'
import { NewsletterFormSubmit, NewsletterFormSubmitProps } from './submit'

interface Props extends React.HTMLProps<HTMLElement> {
  /**
   * onSubmit action call call. Value contains form values
   */
  onSubmit?: (value: object) => boolean
  /**
   * form action value
   */
  action: string
  /**
   * form method. POST, GET, PUT, etc.
   */
  method?: string
  /**
   * child elements to render within the form. (i.e. form fields, buttons, etc.)
   */
  children?: any
  /**
   * additional form classes to add
   */
  classes?: string
  /**
   * styles object
   */
  styles?: object
}

const NewsletterForm: React.FunctionComponent<Props> = ({
  action,
  method = 'POST',
  classes = '',
  styles = {},
  children = [],
  onSubmit = (): boolean => true,
}: Props) => {
  const defaultChildren = [
    <NewsletterEmail />,
    <NewsletterFormSubmit>Sign Up</NewsletterFormSubmit>, // TODO: @frontend needs translation.
  ]

  const [formData, setFormData] = useState({})

  const onFormInit = (defaults: object): void => {
    setFormData(defaults)
  }

  const onFormChange = (values: object): void => {
    const data = { ...formData, ...values }
    setFormData(data)
  }

  const submit = (e: any): boolean => {
    const valid = onSubmit(formData)

    if (!valid && e) {
      e.preventDefault()
    }

    return valid
  }

  const formChildren = children && children.length ? children : defaultChildren

  return (
    <form
      action={action}
      method={method}
      encType="multipart/form-data"
      className={`bc-newsletter-form ${classes}`}
      onSubmit={submit}
      style={styles}
    >
      {
        React.Children.map(formChildren, child =>
          child
            ? React.cloneElement(child, {
                formData,
                onFormChange,
                onFormInit,
              } as any)
            : null
        ) as any
      }
    </form>
  )
}

export {
  NewsletterForm,
  NewsletterFormSubmit,
  NewsletterEmail,
  NewsletterFormSubmitProps,
  NewsletterEmailProps,
  Props as NewsletterFormProps,
}
