import { useEffect, useState } from 'react'

export interface ProductDataFetchType {
  getBrandName?: Function
  getProduct?: Function
  getProductImages?: Function
  getCustomFields?: Function
  getStorefront?: Function
  getProductReviews?: Function
  getProductOptions?: Function
  getProductModifiers?: Function
}

const useProductData: any = (productDataFunctions: ProductDataFetchType, productId: string) => {
  const [hasError, setErrors] = useState(false)
  const [product, setProducts] = useState({ brand_id: null })
  const [brand, setBrands] = useState({})
  const [image, setImages] = useState({})
  const [specs, setSpecs] = useState({})
  const [store_config, setConfig] = useState({})
  const [reviews, setReviews] = useState({})
  const [modifiers, setModifiers] = useState([])
  const [options, setOptions] = useState([])

  useEffect(() => {
    if (productDataFunctions.getStorefront) {
      productDataFunctions
        .getStorefront()
        .then((res): void => setConfig(res))
        .catch(err => setErrors(err))
    }

    if (productDataFunctions.getProduct) {
      productDataFunctions
        .getProduct({ id: productId })
        .then((res): void => setProducts(res))
        .catch(err => {
          setErrors(err)
        })
    }

    if (productDataFunctions.getProductImages) {
      productDataFunctions
        .getProductImages(productId)
        .then((res: object): void => setImages(res))
        .catch(err => {
          setErrors(err)
        })
    }

    if (productDataFunctions.getCustomFields) {
      productDataFunctions
        .getCustomFields({ id: productId })
        .then((res): void => setSpecs(res))
        .catch(err => {
          setErrors(err)
        })
    }
    if (productDataFunctions.getProductModifiers) {
      productDataFunctions
        .getProductModifiers({ id: productId })
        .then((res: object): void => setModifiers(res as any))
        .catch(err => {
          setErrors(err)
        })
    }

    if (productDataFunctions.getProductOptions) {
      productDataFunctions
        .getProductOptions({ id: productId })
        .then((res: object): void => setOptions(res as any))
        .catch(err => {
          setErrors(err)
        })
    }

    if (productDataFunctions.getProductReviews) {
      productDataFunctions
        .getProductReviews({ id: productId })
        .then((res): void => setReviews(res))
        .catch(err => {
          setErrors(err)
        })
    }
  }, [productId])

  useEffect(() => {
    if (productDataFunctions.getBrandName && product && product.brand_id) {
      productDataFunctions
        .getBrandName({ id: product.brand_id })
        .then((res): void => setBrands(res))
        .catch(err => {
          setErrors(err)
        })
    }
  }, [product])

  return { product, brand, image, specs, store_config, reviews, hasError, options, modifiers }
}

export { useProductData }
