import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type ProductImageTheme = Partial<{
  base: CSS
}>

export const ProductImage: ProductImageTheme = {
  base: ({ colors }) => css`
    background: ${colors?.['neutral-95']};
    width: fit-content;
    position: relative;
    img {
      opacity: 1;
      max-height: 300px;
      position: relative;
      transition: opacity 0.25s ease-in-out;
      display: block;
      max-width: 100%;
    }
  `,
}

export function useProductImageStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.ProductImage?.base
}
