/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Role } from 'reakit/Role'

import { useStartRatingStyle } from '../../../theme/start-rating'

export type StarRatingProps<T = React.HTMLProps<HTMLElement>> = T & {
  rating: number
  prefix?: string
  as?: React.ComponentType<T> | React.ElementType
}

function StarRating(props: StarRatingProps): React.ReactElement {
  const { rating, prefix, as = 'div', ...rest } = props

  const css = useStartRatingStyle()
  const ratePercentage = (rating / 5) * 100
  const setRatingPercentage = { width: `${ratePercentage}%` }

  return (
    <Role css={css} as={as} {...rest}>
      {prefix && React.createElement('span', { className: 'ProductReview__Prefix' }, prefix)}
      <div className="ProductReview__Rating">
        <div className="ProductReview__RatingTop" style={setRatingPercentage}>
          {[...Array(5)].map(() => (
            <span>★</span>
          ))}
        </div>
        <div className="ProductReview__RatingBottom">
          {[...Array(5)].map(() => (
            <span>☆</span>
          ))}
        </div>
      </div>
    </Role>
  )
}

export { StarRating }
