import { css, useTheme } from '@emotion/react'

import { getBaseStyle } from '../utils'
import { CSS, CSSPrimitive } from './types'

export type ButtonVariant = 'primary' | 'secondary' | 'tertiary' | 'link' | 'selector'

export type ButtonTheme = Partial<{
  base: CSS
  variants: Record<ButtonVariant, CSS>
}>

export const Button: ButtonTheme = {
  base: theme => css`
    ${theme.typography.body as CSSPrimitive}
    outline: 0;
    padding: 12px 32px;
    border: 2px solid transparent;
    text-align: center;
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.15s ease-in-out;
    font-size: 19px;
    line-height: 0;
    border-radius: 26px;
    height: 48px;
    &:focus {
      text-decoration: underline;
    }
  `,
  variants: {
    primary: theme => css`
      ${getBaseStyle(theme?.components?.Button?.base, theme)}
      background-color: ${theme.colors?.primary};
      color: ${theme.colors?.['neutral-0']};
      font-weight: 900;
      font-family: 'Red Hat Display', sans-serif;
      &[disabled],
      &[aria-disabled='true'] {
        background-color: ${theme.colors['neutral-55']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          background-color: ${theme.colors['primary-60']};
        }
      }
    `,
    secondary: theme => css`
      ${getBaseStyle(theme?.components?.Button?.base, theme)}
      background-color: ${theme.colors?.['neutral-90']};
      color: ${theme.colors?.['neutral-0']};
      font-weight: 900;
      font-family: 'Red Hat Display', sans-serif;
      &[disabled],
      &[aria-disabled='true'] {
        background-color: ${theme.colors['neutral-55']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          background-color: ${theme.colors['neutral-100']};
        }
      }
    `,
    tertiary: theme => css`
      ${getBaseStyle(theme?.components?.Button?.base, theme)}
      background-color: ${theme.colors?.['neutral-0']};
      color: ${theme.colors?.['neutral-95']};
      border-color: ${theme.colors?.['neutral-95']};
      &[disabled],
      &[aria-disabled='true'] {
        border-color: ${theme.colors['neutral-55']};
        color: ${theme.colors['neutral-55']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          border-color: ${theme.colors['neutral-55']};
        }
      }
    `,
    link: theme => css`
      ${theme.typography.body as CSSPrimitive}
      background-color: transparent;
      border: none;
      color: ${theme.colors?.['neutral-95']};
      text-decoration: underline;
      text-underline-offset: 2px;
      font-size: 16px;
      line-height: 24px;
      cursor: pointer;
      &:focus {
        text-decoration: none;
      }
      &[disabled],
      &[aria-disabled='true'] {
        color: ${theme.colors['neutral-55']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          color: ${theme.colors.primary};
        }
      }
    `,
    selector: theme => css`
      ${getBaseStyle(theme?.components?.Button?.base, theme)}
      padding: 4px 16px;
      ${theme.typography['body-small'] as CSSPrimitive}
      height: 40px;
      background-color: ${theme.colors?.['neutral-0']};
      color: ${theme.colors?.['neutral-95']};
      &[data-selected='true'] {
        background-color: ${theme.colors?.['neutral-95']};
        color: ${theme.colors?.['neutral-0']};
      }
      border-color: ${theme.colors?.['neutral-95']};
      &[disabled],
      &[aria-disabled='true'] {
        border-color: ${theme.colors['neutral-55']};
        color: ${theme.colors['neutral-55']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          border-color: ${theme.colors['neutral-55']};
        }
      }
    `,
  },
}

export function useButtonStyle(variant: ButtonVariant): CSS {
  const theme = useTheme()
  return theme?.components?.Button?.variants?.[variant]
}
