import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type WishlistDetailTheme = Partial<{
  Header: CSS
  ProductGrid: CSS
  ProductList: CSS
}>

export const WishlistDetail: WishlistDetailTheme = {
  Header: ({ typography }) => css`
    ${typography.display as CSSPrimitive}
    text-align: center;
    margin-bottom: 20px;
  `,
  ProductGrid: () => css`
    flex-direction: row;
    align-items: center;
    justify-content: center;
    display: flex;
    flex: 1;
  `,
  ProductList: () => css`
    margin: 0 auto;
    max-width: 700px;
  `,
}

export function useWishlistDetailStyle(): WishlistDetailTheme {
  const theme = useTheme()
  return theme?.components?.WishlistDetail
}
