/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Role } from 'reakit/Role'

import { TypographyVariant, useTypographyStyle } from '../theme/typography'

export type TypographyProps<T = React.HTMLProps<HTMLElement>> = T & {
  as?: React.ComponentType<T> | React.ElementType
  variant?: TypographyVariant
}

export function Typography(props: TypographyProps): React.ReactElement {
  const { as = 'p', variant = 'body', css: overrideCss, ...rest } = props
  const css = useTypographyStyle(variant)
  // TODO: Support this properly
  // @ts-ignore
  return <Role as={as} css={[css, overrideCss]} {...rest} />
}
