import classnames from 'classnames'
import React from 'react'

import { OrderRow } from './order-row'
import { Order } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  orders: Order[]
  OrderComponent?: any
  styles?: any
  productMap: any
  onOrderAction?: (action: string, order: Order) => void
  className?: string
}

const OrderList: React.FunctionComponent<Props> = ({
  orders = [],
  styles = {},
  productMap = {},
  OrderComponent = OrderRow,
  onOrderAction = () => {},
  className = '',
}: Props) => {
  return (
    <div className={classnames(styles.orders, className)}>
      {orders.map(order => (
        <OrderComponent
          onOrderAction={onOrderAction}
          products={productMap[order.id]}
          key={order.id}
          order={order}
          styles={styles}
        />
      ))}
    </div>
  )
}

export { OrderList, Props as OrderListProps }
