/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Button } from 'src/components/utilities/button'

import { useTranslation } from '@hooks/use-translation'
import { useProfileFormStyle } from '@theme/profile-form'

interface FormField {
  name: string
  label: string
  required?: boolean
  inline?: boolean
  component?: any
  type?: string
}

interface FormSection {
  name: string
  fields: FormField[]
}

export interface ProfileFormProps extends React.HTMLProps<HTMLElement> {
  onSubmit: (value: object) => boolean
  onFieldChange?: (name: string, value: any) => void
  initialValues?: { [key: string]: any }
  formConfig?: FormSection[]
}

export const defaultFormConfig: FormSection[] = [
  {
    name: 'GENERAL INFO',
    fields: [
      {
        name: 'firstName',
        label: 'First name',
        required: true,
        inline: true,
      },
      {
        name: 'company',
        label: 'Company name',
        required: false,
        inline: true,
      },
      {
        name: 'lastName',
        label: 'Last name',
        required: true,
      },
    ],
  },
  {
    name: 'CONTACT INFO',
    fields: [
      {
        name: 'email',
        label: 'Email address',
        required: true,
        inline: true,
      },
      {
        name: 'phone',
        label: 'Phone number',
        required: false,
        inline: true,
      },
    ],
  },
  {
    name: 'CHANGE PASSWORD',
    fields: [
      {
        name: 'currPwd',
        label: 'Current password',
        required: true,
        type: 'password',
      },
      {
        name: 'oldPwd',
        label: 'Old password',
        required: true,
        inline: true,
        type: 'password',
      },
      {
        name: 'newPwd',
        label: 'New password',
        required: true,
        inline: true,
        type: 'password',
      },
    ],
  },
]

const ProfileForm: React.FunctionComponent<ProfileFormProps> = ({
  onSubmit,
  onFieldChange,
  action,
  formConfig = defaultFormConfig,
  initialValues = {},
  className,
}: ProfileFormProps) => {
  const { t } = useTranslation()
  const [values, setValues] = React.useState(initialValues)
  const styles = useProfileFormStyle()

  const handleSubmit = (e): void => {
    e.preventDefault()
    onSubmit(values)
  }

  const handleChange = (e): void => {
    if (onFieldChange) onFieldChange(e.target.name, e.target.value)
    setValues({ ...values, [e.target.name]: e.target.value })
  }

  return (
    <form
      className={className}
      css={styles.Form}
      action={action}
      method="POST"
      encType="multipart/form-data"
      onSubmit={handleSubmit}
    >
      {Object.entries(formConfig).map(([key, fieldSet]) => (
        <fieldset css={styles.Fieldset} key={key}>
          <h3 css={styles.Heading}>{fieldSet.name}</h3>
          {fieldSet.fields.map(({ name, label, inline, type, component: Component, required }) => {
            const id = `profile_form_${fieldSet.name}_${name}`
            const inputProps = {
              id,
              key: id,
              name,
              onChange: handleChange,
              required,
              type,
              value: values[name] ?? '',
            }

            return (
              <div key={name} css={styles.Field?.(inline ? 'inline' : 'block')}>
                <label css={styles.Label} htmlFor={id}>
                  {t(`bc.fields.${name}`, label)}{' '}
                  {!required && <span css={styles.LabelOptional}>(optional)</span>}
                </label>
                {Component ? (
                  <Component {...inputProps} />
                ) : (
                  <input css={styles.Input} {...inputProps} />
                )}
              </div>
            )
          })}
        </fieldset>
      ))}
      <Button variant="secondary" type="submit">
        Update details
      </Button>
    </form>
  )
}

export { ProfileForm }
