import { useContext } from 'react'

import { CommerceComponentsContext, TranslationFn } from '../components/provider'

/**
 * React hook that returns the translation function that is set in CommerceComponentsContext
 * Defaults to (key, defaultString) => defaultString when context is not set by the user
 * @returns TranslationFn
 */
export const useTranslation: () => { t: TranslationFn } = () => {
  const { t } = useContext(CommerceComponentsContext)

  return { t }
}
