/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Clickable } from 'reakit/Clickable'

import { useTranslation } from '@hooks/use-translation'
import { useAddressCardStyle } from '@theme/address-card'
import { CSSPrimitive } from '@theme/types'

import { AddressType } from './types'

export interface AddressCardProps extends React.HTMLProps<HTMLElement> {
  address: AddressType
  onDelete?: (address: AddressType) => void
  onEdit?: (address: AddressType) => void
}

const AddressCard: React.FunctionComponent<AddressCardProps> = ({
  address,
  onDelete,
  onEdit,
}: AddressCardProps) => {
  const { t } = useTranslation()
  const styles = useAddressCardStyle()

  return (
    <div css={styles.Container}>
      <h4 css={styles.Name}>
        {address.first_name} {address.last_name}
      </h4>
      <p css={styles.AddressLine}>{address.address1}</p>
      {!!address.address2 && <p css={styles.AddressLine}>{address.address2}</p>}
      <p css={styles.AddressLine}>
        {address.city}, {address.state_or_province} {address.postal_code}
      </p>
      <p css={styles.AddressLine}>{address.country}</p>

      <p css={styles.Phone}>{address.phone}</p>

      {onEdit && (
        <Clickable
          css={[styles.ActionButton, styles.EditButton] as CSSPrimitive[]}
          onClick={() => onEdit(address)}
        >
          {t('bc.btn.edit', 'Edit')}
        </Clickable>
      )}
      {onDelete && (
        <Clickable
          css={[styles.ActionButton, styles.DeleteButton] as CSSPrimitive[]}
          onClick={() => onDelete(address)}
        >
          {t('bc.btn.delete', 'Delete')}
        </Clickable>
      )}
    </div>
  )
}

export { AddressCard }
