/** @jsx jsx */
import { jsx } from '@emotion/react'
import React from 'react'
import { Button } from 'reakit/Button'
import { Role, RoleProps } from 'reakit/Role'

import { usePaginationStyle } from '../../../theme/pagination'
import { usePagination, UsePaginationProps } from './use-pagination'

export type PaginationProps = Omit<
  UsePaginationProps & Omit<RoleProps, 'onChange' | 'disabled'>,
  'componentName'
>

export function Pagination(props: PaginationProps): React.ReactElement {
  const { css } = props
  const items = usePagination(props)
  const styles = usePaginationStyle()

  return (
    // @ts-ignore
    <Role as="ul" css={[styles.Container, css]}>
      {items.map(item => {
        const { type, page, selected, disabled, onClick } = item
        const key = type + page
        if (type === 'previous' || type === 'next') {
          return (
            <li key={key}>
              <Button
                onClick={onClick}
                disabled={disabled || selected}
                css={type === 'previous' ? styles.Previous : styles.Next}
              >
                <svg width={8} height={12} viewBox="0 0 8 12" fill="none">
                  <path
                    d={type === 'previous' ? 'M6 10L2 6l4-4' : 'M2 2l4 4-4 4'}
                    stroke="currentColor"
                    strokeWidth={2}
                    strokeMiterlimit={10}
                    strokeLinecap="square"
                  />
                </svg>
              </Button>
            </li>
          )
        }
        if (type === 'start-ellipsis' || type === 'end-ellipsis') {
          return (
            <li key={key}>
              <div css={styles.Ellipsis}>...</div>
            </li>
          )
        }
        if (type === 'page') {
          return (
            <li key={key}>
              <Button
                disabled={disabled || selected}
                aria-current={selected}
                onClick={onClick}
                css={styles.Page}
              >
                {page}
              </Button>
            </li>
          )
        }
        return null
      })}
    </Role>
  )
}
