import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type CardTheme = Partial<{
  variants: {
    medium: (imageUrl: string) => CSS
    large: (imageUrl: string) => CSS
  }
}>

export type CardVariant = 'medium' | 'large'

export const Card: CardTheme = {
  variants: {
    medium: imageUrl => ({ typography, colors }) => css`
      ${typography['display-small'] as CSSPrimitive};
      color: ${colors['neutral-0'] as CSSPrimitive};
      background-image: url(${imageUrl});
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 60px 24px;
      text-decoration: none;
    `,
    large: imageUrl => ({ typography, colors }) => css`
      ${typography['display-x-large'] as CSSPrimitive};
      color: ${colors['neutral-95'] as CSSPrimitive};
      display: flex;
      background-image: url(${imageUrl});
      justify-content: flex-start;
      align-items: center;
      padding: 45px 64px;
      text-decoration: none;
    `,
  },
}

export function useCardStyle(imageUrl: string, variant = 'medium'): CSS {
  const theme = useTheme()
  return theme?.components?.Card?.variants?.[variant]?.(imageUrl)
}
