import map from 'lodash/map'
import React from 'react'

interface Country {
  id: string
  sortname: string
  name: string
}

interface Props extends React.HTMLProps<HTMLElement> {
  onChange: (e: React.FormEvent) => void
  name: string
  value?: string
  id: string
  countries?: Country[]
}

const CountrySelect: React.FunctionComponent<Props> = ({
  onChange = () => {},
  name = 'country',
  value = 'US',
  className,
  id = 'country',
  countries = [],
}: Props) =>
  countries && countries.length ? (
    <select id={id} name={name} value={value} className={className} onChange={onChange}>
      {map(countries, country => (
        <option key={country.id} value={country.sortname}>
          {country.name}
        </option>
      ))}
    </select>
  ) : (
    <input id={id} name={name} value={value} className={className} onChange={onChange} />
  )

export { CountrySelect, Props as CountrySelectProps }
