import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type AddressCardTheme = Partial<{
  Container: CSS
  Name: CSS
  AddressLine: CSS
  Phone: CSS
  ActionButton: CSS
  EditButton: CSS
  DeleteButton: CSS
}>

export const AddressCard: AddressCardTheme = {
  Container: ({ colors }) => css`
    border: 1px solid ${colors['neutral-15']};
    padding: 32px;
    max-width: 380px;
  `,
  Name: ({ typography }) => css`
    ${typography['display-small'] as CSSPrimitive}
    padding-bottom: 24px;
  `,
  AddressLine: ({ typography }) => css`
    ${typography.body as CSSPrimitive}
    margin-bottom: 8px;
  `,
  Phone: ({ typography }) => css`
    ${typography.body as CSSPrimitive}
    margin-top: 16px;
    padding-left: 20px;
    background-image: url('data:image/svg+xml;base64,');
    background-repeat: no-repeat;
    background-position: left center;

    & + button {
      margin-top: 40px;
    }
  `,
  ActionButton: ({ typography }) => css`
    ${typography.body as CSSPrimitive}
    background: none;
    border: none;
    text-decoration: underline;
    padding: 0 0 0 25px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: left center;
    font-size: 16px;

    &:not(:last-child) {
      margin-right: 24px;
    }

    &:hover {
      text-decoration: none;
    }
  `,
  EditButton: () => css`
    background-image: url('data:image/svg+xml;base64,');
  `,
  DeleteButton: () => css`
    background-image: url('data:image/svg+xml;base64,');
  `,
}

export function useAddressCardStyle(): AddressCardTheme {
  const theme = useTheme()
  return theme?.components?.AddressCard
}
