import map from 'lodash/map'
import React from 'react'

interface State {
  id: string
  name: string
}

interface Props extends React.HTMLProps<HTMLElement> {
  onChange: (e: React.FormEvent) => void
  name: string
  value?: string
  id: string
  states: State[]
}

const StateSelect: React.FunctionComponent<Props> = ({
  onChange = () => {},
  name = 'state',
  value,
  className,
  id = 'state',
  states = [],
}: Props) => {
  if (!states.length) {
    return <input id={id} name={name} value={value} className={className} onChange={onChange} />
  }

  return (
    <select id={id} name={name} value={value} className={className} onChange={onChange}>
      {map(states, state => (
        <option key={state.id} value={state.name}>
          {state.name}
        </option>
      ))}
    </select>
  )
}

export { StateSelect, Props as StateSelectProps }
