import classnames from 'classnames'
import React, { useState } from 'react'

import { useTranslation } from '@hooks/use-translation'

import { ReviewFormField, ReviewType } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  onSubmit: (value: object) => boolean
  children?: any
  formConfig?: ReviewFormField[]
  styles?: any
  fill?: ReviewType
  className?: string
}

const TextArea: React.FunctionComponent = (props: any) => <textarea {...props} />
const RatingSelect: React.FunctionComponent = (props: any) => (
  <select {...props}>
    {[1, 2, 3, 4, 5].map(rating => (
      <option value={rating}>{rating}</option>
    ))}
  </select>
)

const defaultFormConfig = [
  {
    name: 'rating',
    label: 'Rating',
    required: true,
    component: RatingSelect,
  },
  {
    name: 'name',
    label: 'Name',
    required: true,
  },
  {
    name: 'email',
    label: 'Email Address',
    required: true,
    type: 'email',
  },
  {
    name: 'subject',
    label: 'Subject',
    required: true,
  },
  {
    name: 'comments',
    label: 'Comments',
    required: true,
    component: TextArea,
  },
]

export const getFormConfig = (): ReviewFormField[] => defaultFormConfig

const ReviewForm: React.FunctionComponent<Props> = ({
  onSubmit,
  action,
  children = [],
  formConfig = defaultFormConfig,
  fill,
  styles = {},
  className = '',
}: Props) => {
  const { t } = useTranslation()
  const [review, setReview] = useState(fill || {})

  const onFieldChange = (e: React.FormEvent<HTMLInputElement>): void => {
    const { name, value } = e.target as any
    const newReview = { ...review }
    newReview[name] = value
    setReview(newReview)
  }

  const fields = formConfig.map(field => {
    const fieldProps = {
      name: field.name,
      required: field.required,
      id: `review-form-${field.name}`,
      className: styles[field.name],
      value: review ? review[field.name] : '',
      key: `review_${field.name}`,
    }
    const FieldComponent = field.component
    const classes = [styles.fieldWrapper, styles[`${field.name}-wrapper`]].join(' ') as string

    return (
      <div key={`field-${field.name}`} className={classes}>
        <label className={styles[`${field.name}-label`]} htmlFor={`review-form-${field.name}`}>
          {t(`bc.fields.${field.name}`, field.label)}
          {field.required && <span className={styles.required}>*</span>}
        </label>
        {FieldComponent ? (
          <FieldComponent review={review} onChange={onFieldChange} {...fieldProps} />
        ) : (
          <input onChange={onFieldChange} {...fieldProps} />
        )}
      </div>
    )
  })

  const submit = (e): boolean => {
    e.preventDefault()
    const formData = new FormData(e.target)
    const newReview = {}
    formData.forEach((value, key) => {
      newReview[key] = value
    })
    return onSubmit({ ...e, data: newReview })
  }

  return (
    <form
      className={classnames(styles.reviewForm, className)}
      action={action}
      method="POST"
      encType="multipart/form-data"
      onSubmit={submit}
    >
      {fields}
      {children}
      <button className={styles.saveButton} type="submit">
        {t('bc.reviews.save', 'Save Review')}
      </button>
    </form>
  )
}

export { ReviewForm, Props as ReviewFormProps }
