import * as React from 'react'

interface Props extends React.HTMLProps<HTMLElement> {
  label?: string
  placeholder?: string
  type?: string
  styles?: any
  className?: string
}

const NewsletterEmail: React.FunctionComponent<Props> = ({
  type = 'email',
  placeholder = 'Email',
  label = 'Email Address',
  styles = {},
  className = '',
}: Props) => {
  return (
    <div className={className}>
      <label className={styles.usernameLabel} key="username-label" htmlFor="username">
        {label}
      </label>
      <input
        key="username-field"
        type={type}
        name="username"
        id="username"
        placeholder={placeholder}
        className={styles.username}
      />
    </div>
  )
}

export { NewsletterEmail, Props as NewsletterEmailProps }
