import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type StartRatingTheme = Partial<{
  base: CSS
}>

export const StartRating: StartRatingTheme = {
  base: ({ colors }) => css`
    display: block;
    margin: 8px 0;
    .ProductReview__Rating {
      display: inline-block;
      position: relative;
      vertical-align: middle;
    }

    .ProductReview__RatingTop {
      overflow: hidden;
      color: ${colors['neutral-90']};
      position: absolute;
      top: 0;
      left: 0;
    }

    .ProductReview__RatingBottom {
      color: ${colors['neutral-90']};
    }

    .ProductReview__Prefix {
      display: inline-block;
      margin-right: 12px;
      vertical-align: middle;
      font-weight: bold;
    }
  `,
}

export function useStartRatingStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.StartRating?.base
}
