/** @jsx jsx */
import { jsx } from '@emotion/react'
import format from 'date-fns/format'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'
import { StatusVariants } from '@theme/order-detail'
import { useOrderRowStyle } from '@theme/order-row'

import { ProductPrice } from '../product'
import { Order, OrderProduct } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  order: Order
  products: OrderProduct[]
  onOrderAction?: (action: string, order: Order) => void
  statusVariant?: StatusVariants
  className?: string
}

const OrderRow: React.FunctionComponent<Props> = ({
  order,
  products,
  onOrderAction = () => {},
  statusVariant,
  className = '',
}: Props) => {
  const styles = useOrderRowStyle()
  const { t } = useTranslation()
  const { id, date_created, custom_status, total_inc_tax, currency_code } = order
  const price = parseFloat(total_inc_tax)
  const onDetailClick = (): void => onOrderAction('details', order)

  return (
    <div css={styles.OrderRow} className={className}>
      <div css={styles.Header}>
        <span css={styles.OrderNumber}>
          {t('bc.order.order', 'Order')} #{id}
        </span>
        <div css={styles.OrderStatus(statusVariant)}>{custom_status}</div>
        <button type="button" onClick={onDetailClick} css={styles.OrderDetailsButton}>
          {t('bc.order.details', 'Order Details')}
        </button>
      </div>
      <div css={styles.Products}>
        {products?.map(product => (
          <div key={product.id} css={styles.Product}>
            {product.image_url && (
              <img css={styles.ProductImage} src={product.image_url} alt={product.name} />
            )}
            <div css={styles.ProductText}>
              {product.name}
              <span css={styles.ProductQuantity}>{product.quantity} &times;</span>
            </div>
          </div>
        ))}
      </div>

      <div>
        <div css={styles.InfoRow}>
          <span>{t('bc.order.ordered', 'Ordered')}</span>
          <span>{format(new Date(date_created), 'MMM dd, yyyy')}</span>
        </div>
        <div css={styles.InfoRow}>
          <span>{t('bc.order.total', 'Total (USD)')}</span>
          <strong>
            <ProductPrice
              price={price}
              salePrice={0}
              currencySettings={{ currency: currency_code }}
              css={styles.OrderPrice}
            />
          </strong>
        </div>
      </div>
    </div>
  )
}

export { OrderRow, Props as OrderRowProps }
