import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type QuantitySelectorTheme = Partial<{
  base: CSS
}>

// TODO: Reuse (extend) styles from Button
export const QuantitySelector: QuantitySelectorTheme = {
  base: ({ colors }) => css`
    border: 2px solid ${colors['neutral-15']};
    border-radius: 24px;
    width: fit-content;
    min-width: 152px;
    min-height: 48px;
    display: flex;
    justify-content: space-between;
    .QuantitySelector__Value {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .QuantitySelector__More,
    .QuantitySelector__Minus {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 48px;
      border-radius: 24px;
      border: none;
      background-color: transparent;
      cursor: pointer;
      path {
        stroke: ${colors['neutral-95']};
      }
      :hover {
        path {
          stroke: ${colors['neutral-55']};
        }
      }
    }
  `,
}

export function useQuantitySelectorStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.QuantitySelector?.base
}
