import { css, SerializedStyles, Theme, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type CheckboxTheme = Partial<{
  base: CSS
}>

export const Checkbox: CheckboxTheme = {
  base: (theme: Theme): SerializedStyles => css`
    padding: 24px 0;
    display: flex;
    align-items: center;
    p {
      margin-left: 16px;
      display: inline-block;
      ${theme.typography['body-large'] as CSSPrimitive}
    }
  `,
}

export function useCheckboxStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.Checkbox.base
}
