import React from 'react'

import { useProductImages } from '../../hooks/product-images'

interface Product {
  id: string
}

interface Props extends React.HTMLProps<HTMLElement> {
  getProductImages: Function
  products: Product[]
  children: any
  defaultImage: object
}

const ProductsImagesProvider: React.FunctionComponent<Props> = ({
  getProductImages,
  products,
  children,
  defaultImage = {},
}: Props) => {
  const images = useProductImages(getProductImages, products)
  return React.Children.map(children, child => {
    const { product } = child.props
    const image = product && images[product.id] ? images[product.id] : defaultImage
    return child ? React.cloneElement(child, { image } as any) : null
  })
}

export { ProductsImagesProvider, Props as ProductsImagesProviderProps }
