import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type ProductTitleTheme = Partial<{
  base: CSS
}>

export const ProductTitle: ProductTitleTheme = {
  base: css`
    padding: 8px 0;
  `,
}

export function useProductTitleStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.ProductTitle?.base
}
