import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

type ProductRowComponents =
  | 'Container'
  | 'Image'
  | 'Content'
  | 'Options'
  | 'Price'
  | 'PriceQuantity'
  | 'Quantity'
  | 'Delete'
  | 'Variant'

export type ProductRowTheme = Partial<Record<ProductRowComponents, { base: CSS }>>

export const ProductRow: ProductRowTheme = {
  Container: {
    base: theme => css`
      display: flex;
      border-bottom: 2px solid ${theme.colors['neutral-15']};
      padding: 24px 0;
      :first-of-type {
        border-top: 2px solid ${theme.colors['neutral-15']};
      }
    `,
  },
  Image: {
    base: () => css`
      width: 100%;
      height: 100%;

      display: none;
      margin-right: 40px;
      max-width: 160px;
      max-height: 160px;
      @media (min-width: 768px) {
        display: initial;
      }
    `,
  },
  Content: {
    base: () => css`
      display: flex;
      flex: 1;
      justify-content: space-between;
      > div {
        display: flex;
        flex-direction: column;
      }
    `,
  },
  Options: {
    base: theme => css`
      ${theme.typography['body-bold'] as CSSPrimitive}
    `,
  },
  Price: {
    base: () => css`
      justify-content: flex-end;
      display: inline-flex;
      white-space: pre;
    `,
  },
  PriceQuantity: {
    base: () => css`
      margin-right: 5px;
    `,
  },
  Quantity: {
    base: () => css`
      margin-top: auto;
    `,
  },
  Delete: {
    base: theme => css`
      margin-top: auto;
      width: fit-content;
      align-self: flex-end;
      background-color: transparent;
      border: none;
      cursor: pointer;
      path {
        stroke: ${theme.colors['neutral-40']};
      }
      :hover {
        path {
          stroke: ${theme.colors.attention};
        }
      }
    `,
  },
  Variant: {
    base: theme => css`
      ${theme.typography['body-bold'] as CSSPrimitive}
    `,
  },
}

export function useProductRowStyle(): Partial<Record<ProductRowComponents, CSS>> {
  const theme = useTheme()

  return Object.entries(theme?.components?.ProductRow).reduce(
    (
      acc: Partial<Record<ProductRowComponents, CSS>>,
      [key, value]: [ProductRowComponents, { base: CSS }]
    ) => {
      return {
        ...acc,
        [key]: value?.base,
      }
    },
    {}
  )
}
