/** @jsx jsx */
import { jsx } from '@emotion/react'
import { FunctionComponent } from 'react'
import { Role } from 'reakit'

import { CardVariant, useCardStyle } from '../../../theme/card'

export type CardProps<T = React.HTMLProps<HTMLElement>> = T & {
  name: React.ReactNode
  imageUrl: string
  as?: React.ComponentType<T> | React.ElementType
  variant?: CardVariant
}

export const Card: FunctionComponent<CardProps> = ({
  name,
  imageUrl,
  variant = 'medium',
  as = 'div',
  css,
  ...rest
}: CardProps) => {
  const styles = useCardStyle(imageUrl, variant)

  return (
    // @ts-ignore
    <Role as={as} css={[styles, css]} {...rest}>
      {name}
    </Role>
  )
}
