/**
 * @function ProductDetailPage
 */

import * as React from 'react'

import {
  Brand,
  Description,
  ProductCondition,
  ProductImage,
  ProductPrice,
  ProductSKU,
  ProductSpecs,
  ProductTitle,
} from '../../core/product'
import * as Form from '../../core/product/form'
import { ProductModifiers } from '../../core/product/modifiers'
import { StarRating } from '../../utilities/star-rating'
import { ProductReviews } from '../product-reviews'

import style from './product-detail-page.module.css'

interface ProductObject {
  name: string
  description: string
  condition: string
  price: number
  sale_price: number
  sku: string
  weight: number
  width: number
  height: number
  depth: number
  reviews_count: number
  reviews_rating_sum: number
}

interface ImageObject {
  url_standard: string
  meta: string
}

interface BrandObject {
  name: string
}

interface Props extends React.HTMLProps<HTMLElement> {
  product: ProductObject
  image: ImageObject
  brand: BrandObject
  reviews?: object
  specs?: string[]
  currencySettings?: object
  PDPClasses?: string
  PDPStyles?: object
  onBuy?: (value: object) => boolean
  options?: object[]
  modifiers?: object[]
}

const ProductDetailPage: React.FunctionComponent<Props> = (props: Props) => {
  const {
    product,
    options = [],
    modifiers = [],
    image,
    brand,
    currencySettings = {},
    specs = [],
    reviews = {},
    PDPClasses = '',
    PDPStyles = {},
    onBuy = () => {},
  } = props

  const productOptions = [...options, ...modifiers]

  const children = props.children ? (
    props.children
  ) : (
    <>
      <ProductImage
        src={image.url_standard}
        alt={image.meta}
        // wrapperClasses={style.bcProductDetailPage_image}
      />
      <div className={style.bcProductDetailPage_content}>
        <ProductTitle text={product.name} as="h1" />
        <ProductCondition text={product.condition} classes={style.bcProductDetailPage__condition} />
        {brand && brand.name && (
          <Brand text={`Brand: ${brand.name}`} classes={style.bcProductDetailPage__brand} />
        )}
        <ProductPrice
          as="div"
          price={product.price}
          salePrice={product.sale_price}
          currencySettings={currencySettings}
          className={style.bcProductDetailPage__price}
        />
        <StarRating
          className={style.bcProductDetailPage__reviewTotal}
          rating={product.reviews_rating_sum / product.reviews_count}
          prefix={`${product.reviews_count} Reviews: `}
        />
        <ProductSKU text={`SKU: ${product.sku}`} classes={style.bcProductDetailPage__sku} />
        <Form.ProductForm action="test" onSubmit={onBuy as any} product={product}>
          {productOptions.length ? <ProductModifiers fields={productOptions as any} /> : null}
          <Form.ProductFormSubmit>Add to Cart</Form.ProductFormSubmit>
        </Form.ProductForm>
      </div>
      <div className={style.bcProductDetailPage__details}>
        <Description text={product.description} />
        <ProductSpecs textObject={product} customFields={specs} />
        <div className={style.bcProductDetailPage__reviews}>
          <h3 className="h3">{product.reviews_count} Reviews</h3>
          <ProductReviews reviews={reviews} />
        </div>
      </div>
    </>
  )

  /**
   * The product card children component can be overwritten by adding your own children where ever you implement
   * this component.
   */
  return (
    <div className={`${style.bcProductDetailPage} ${PDPClasses}`} style={PDPStyles}>
      {children}
    </div>
  )
}

export { ProductDetailPage, Props as ProductDetailPageProps }
