import * as React from 'react'

interface Props extends React.HTMLProps<HTMLElement> {
  styles?: any
  className?: string
}

const Password: React.FunctionComponent<Props> = ({ styles = {}, className = '' }: Props) => {
  return (
    <div className={className}>
      <label key="password-label" htmlFor="password" className={styles.passwordLabel}>
        Password
      </label>
      <input
        key="password-field"
        type="password"
        name="password"
        id="password"
        className={styles.password}
      />
    </div>
  )
}

export { Password, Props as PasswordProps }
