import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type FormTheme = Partial<{
  Input: CSS
  Error: CSS
  Label: CSS
}>

export const Form: FormTheme = {
  Input: ({ typography, colors }) => css`
    ${typography.body as CSSPrimitive}
    border: 2px solid ${colors['neutral-15']};
    border-radius: 24px;
    padding: 8px 20px;
    width: 100%;
  `,
  Error: ({ typography, colors }) => css`
    ${typography['body-small'] as CSSPrimitive}
    display: block;
    margin-top: 8px;
    color: ${colors.danger};
  `,
  Label: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    font-weight: 700;
    display: block;
    margin-bottom: 8px;
  `,
}

export function useFormTheme(): FormTheme {
  const theme = useTheme()
  return theme?.components?.Form
}
