import classnames from 'classnames'
import * as React from 'react'

import { Button } from '../../utilities/button'

interface Props extends React.HTMLProps<HTMLElement> {
  styles?: any
  className?: string
  children?: any
}

const LoginButton: React.FunctionComponent<Props> = ({
  children = ['Login'],
  styles = {},
  className = '',
}: Props) => (
  <Button key="submit-button" type="submit" className={classnames(styles.button, className)}>
    {children}
  </Button>
)

export { LoginButton, Props as LoginButtonProps }
