import { css, useTheme } from '@emotion/react'

import { getBaseStyle } from '../utils'
import { CSS, CSSPrimitive } from './types'

export type SideMenuItemVariant = 'active'

export type SideMenuTheme = Partial<{
  base: CSS
  Level: {
    base: CSS
  }
  Item: {
    base: CSS
    variants: Record<SideMenuItemVariant, CSS>
  }
}>

export const SideMenu: SideMenuTheme = {
  base: css``,
  Level: {
    base: ({ typography }) => css`
      > *:not(.SideMenuLevel__title) {
        padding-left: 12px;
      }
      .SideMenuLevel__title {
        ${typography['display-xx-small'] as CSSPrimitive}
        padding-bottom: 24px;
        text-transform: uppercase;
      }
      padding-bottom: 24px;
    `,
  },
  Item: {
    base: ({ typography, colors }) => css`
      > * {
        ${typography.body as CSSPrimitive}
        margin-bottom: 24px;
      }
      button {
        cursor: pointer;
        background-color: transparent;
        border: none;
        :hover {
          color: ${colors.primary};
        }
      }
    `,
    variants: {
      active: theme => css`
        ${getBaseStyle(theme.components?.SideMenu?.Item?.base, theme)}
        > * {
          text-decoration: underline;
        }
      `,
    },
  },
}

export function useSideMenuStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.SideMenu?.base
}
export function useSideMenuLevelStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.SideMenu?.Level?.base
}
export function useSideMenuItemStyle(variant?: SideMenuItemVariant): CSS {
  const theme = useTheme()
  if (!variant) return theme?.components?.SideMenu?.Item?.base
  return theme?.components?.SideMenu?.Item?.variants?.[variant]
}
