import Cookie from 'js-cookie'
import { useEffect, useState } from 'react'

export interface CartHookInputType {
  getCartData: Function
  cookieName?: string
  init?: object
}

export const DEFAULT_CART_COOKIE = 'bc_cartId'

export const useCart: any = ({
  cookieName = DEFAULT_CART_COOKIE,
  getCartData,
  init = undefined,
}: CartHookInputType) => {
  const [cartData, setCartData] = useState(init)
  const cartId = Cookie.get(cookieName)

  const setCart = (data: any): void => {
    if (data && data.id) {
      Cookie.set(cookieName, data.id)
      setCartData(data)
    } else {
      Cookie.remove(cookieName)
      setCartData(undefined)
    }
  }

  useEffect(() => {
    if (cartId) {
      getCartData(cartId)
        .then(data => setCart(data))
        .catch(() => Cookie.remove(cookieName))
    }
  }, [cartId, init])

  return [cartData, setCart]
}
