import { css, useTheme } from '@emotion/react'

import { CSS } from './types'

export type CartTheme = Partial<{
  base: CSS
  Product: {
    base: CSS
  }
}>

export const Cart: CartTheme = {
  base: css``,
}

export function useCartStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.Cart?.base
}
