import { useEffect, useState } from 'react'

const useProductImages: any = (getProductImages, products) => {
  const [images, setImages] = useState({})

  useEffect(() => {
    const ids = products.map(({ id }) => id)
    const requests = ids.map(productId => getProductImages(productId))
    Promise.all(requests)
      .then(allImages => {
        const reduced = allImages.reduce((mem: object, image) => {
          if (!image) {
            return mem
          }

          const newMem = { ...mem }
          // @ts-ignore
          newMem[image.product_id] = image
          return newMem
        }, {})
        // @ts-ignore
        return setImages(reduced)
      })
      .catch(() => {
        // swallow error
      })
  }, [products])

  return images
}

export { useProductImages }
