import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type HeroTheme = Partial<{
  base: CSS
}>

export const Hero: HeroTheme = {
  base: ({ typography, colors }) => css`
    background-color: ${colors['neutral-15']};
    position: relative;
    > * {
      z-index: 1;
    }
    :hover {
      .hero-arrow button {
        opacity: 100%;
      }
    }

    .hero-cta {
      max-width: 100%;
      height: 100%;
      padding: 50px;
      min-height: 500px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: left;
      @media only screen and (min-width: 720px) {
        padding: 64px;

        > * {
          max-width: 500px;
        }
      }
    }
    .hero-headline {
      ${typography.display as CSSPrimitive}
      margin-bottom: 24px;
      @media only screen and (min-width: 720px) {
        ${typography['display-xx-large'] as CSSPrimitive}
      }
    }
    .hero-description {
      margin-bottom: 24px;
    }
    .hero-button {
      width: fit-content;
    }

    .hero-arrow {
      position: absolute;
      z-index: 2;
      top: 0;
      bottom: 0;
      display: flex;
      align-items: center;

      button {
        opacity: 0;
        height: 40px;
        width: 40px;
        background-color: ${colors['neutral-10']};
        border-radius: 100%;
        border-width: 0;
        cursor: pointer;
        transition: opacity 0.15s ease-in;
        :hover {
          opacity: 80%;
        }
        :focus {
          opacity: 100%;
        }
      }
    }
    .hero-arrow--prev {
      left: 5px;
      @media only screen and (min-width: 720px) {
        left: 10px;
      }
      @media only screen and (min-width: 1250px) {
        left: -20px;
      }
    }
    .hero-arrow--next {
      right: 5px;
      button {
        transform: rotate(180deg);
      }
      @media only screen and (min-width: 720px) {
        right: 10px;
      }
      @media only screen and (min-width: 1250px) {
        right: -20px;
      }
    }

    .carousel-root {
      outline: none;
      max-width: initial;
    }

    .hero-image {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
    }

    .carousel {
      position: relative;
      width: 100%;
      height: 100%;
    }
    .carousel img {
      width: 100%;
      display: inline-block;
      pointer-events: none;
    }
    .carousel.carousel-slider {
      position: relative;
      margin: 0;
    }
    .carousel .slider-wrapper {
      overflow: hidden;
      margin: auto;
      width: 100%;
      height: 100%;
      transition: height 0.15s ease-in;
    }
    .carousel .slider-wrapper.axis-horizontal .slider {
      display: flex;
    }
    .carousel .slider-wrapper.axis-horizontal .slider .slide {
      flex-direction: column;
      flex-flow: column;
    }
    .carousel .slider {
      margin: 0;
      padding: 0;
      position: relative;
      list-style: none;
      width: 100%;
      height: 100%;
    }
    .carousel .slider.animated {
      transition: all 0.35s ease-in-out;
    }
    .carousel .slide {
      min-width: 100%;
      margin: 0;
      position: relative;
      text-align: center;
    }
    .carousel .slide img {
      width: 100%;
      vertical-align: top;
      border: 0;
      height: 100%;
      object-fit: cover;
    }
    .carousel .control-dots {
      position: absolute;
      bottom: 0;
      margin: 10px 0;
      padding: 0;
      text-align: center;
      width: 100%;
      z-index: 1;
    }
    @media (min-width: 960px) {
      .carousel .control-dots {
        bottom: 0;
      }
    }
    .carousel .control-dots .dot {
      transition: opacity 0.25s ease-in;
      opacity: 0.3;
      filter: alpha(opacity=30);
      box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9);
      background: #fff;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      cursor: pointer;
      display: inline-block;
      margin: 0 8px;
    }
    .carousel .control-dots .dot.selected,
    .carousel .control-dots .dot:hover {
      opacity: 1;
      filter: alpha(opacity=100);
    }
  `,
}

export function useHeroStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.Hero?.base
}
