/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'

import { NotificationVariant, useNotificationStyle } from '../../../theme/notification'

interface Props extends React.HTMLProps<HTMLDivElement> {
  type?: NotificationVariant // TODO: Rename to variant?
  message: string
  classes?: string
  styles?: object
}

const Notification: React.FunctionComponent<Props> = (props: Props) => {
  const { type = 'success', message, ...rest } = props

  const css = useNotificationStyle(type)

  return (
    <div css={css} {...rest}>
      {message}
    </div>
  )
}

export { Notification, Props as NotificationProps }
