import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { QuantitySelector } from '../../../utilities'

interface Props extends React.HTMLProps<HTMLElement> {
  children: React.ReactChild
  onClick?: (value: object) => void
  onFormChange?: (values: object) => void
}

const ProductFormSubmit: React.FunctionComponent<Props> = ({
  children,
  onClick = () => {},
  onFormChange = () => {},
}: Props) => {
  const { t } = useTranslation()
  // const [quantity, setQuantity] = React.useState<number>(1)
  function onChangeQuantity(quantity: number): void {
    onFormChange({ quantity })
  }
  return (
    <div>
      <QuantitySelector defaultQuantity={1} onChangeQuantity={onChangeQuantity} />
      <button onClick={onClick} type="submit">
        {children || t('Add to Cart')}
      </button>
    </div>
  )
}

export { ProductFormSubmit, Props as ProductFormSubmitProps }
