/** @jsx jsx */
import { jsx } from '@emotion/react'
import cl from 'classnames'
import * as React from 'react'
import { Role } from 'reakit/Role'

import { useProductPriceStyle } from '../../../../theme/product-price'

export type ProductPriceProps<T = React.HTMLProps<HTMLElement>> = T & {
  price: number
  salePrice: number
  currencySettings: object // TODO: Change object to specific types
  as?: React.ComponentType<T> | React.ElementType
}

interface StoreSettings {
  currency?: string
}

const CurrencyFormatter = (settings: StoreSettings, rawPrice: number): string => {
  const { currency } = settings

  const languageCode =
    typeof window !== 'undefined' ? window.navigator.language || 'en-US' : 'en-US'

  return new Intl.NumberFormat(languageCode, {
    style: 'currency',
    currency: currency || 'USD',
  }).format(rawPrice)
}

const ProductPrice: React.FunctionComponent<ProductPriceProps> = (props: ProductPriceProps) => {
  const { price, salePrice, currencySettings, as = 'span', ...rest } = props

  const defaultCss = useProductPriceStyle()

  return (
    <Role as={as} css={defaultCss} {...rest}>
      <span className={cl('ProductPrice__Item', salePrice !== 0 && 'ProductPrice__Item--old')}>
        {CurrencyFormatter(currencySettings, price)}
      </span>
      {salePrice !== 0 && (
        <span className={cl('ProductPrice__Item', 'ProductPrice__Item--sale')}>
          {CurrencyFormatter(currencySettings, salePrice)}
        </span>
      )}
    </Role>
  )
}

export { ProductPrice, CurrencyFormatter }
