/** @jsx jsx */
import { jsx } from '@emotion/react'
import React from 'react'
import { Input } from 'reakit/Input'

import { useFormTheme } from '@theme/form'

export type FieldProps<T = React.HTMLProps<HTMLInputElement | HTMLTextAreaElement>> = Omit<
  T,
  'name' | 'error' | 'label' | 'as'
> & {
  name: string
  error?: string
  label?: React.ReactNode
  as?: React.ComponentType<T> | React.ElementType
}

export const Field: React.FunctionComponent<FieldProps> = ({
  as = 'input',
  className,
  error,
  label,
  name,
  value = '',
  type = 'text',
  ...props
}: FieldProps) => {
  const styles = useFormTheme()

  return (
    <div className={className}>
      {label && (
        <label css={styles.Label} htmlFor={`input-${name}`}>
          {label}
        </label>
      )}
      <Input
        {...props}
        as={as}
        css={styles.Input}
        name={name}
        id={`input-${name}`}
        type={type}
        value={value}
      />
      {error && <span css={styles.Error}>{error}</span>}
    </div>
  )
}
