import * as React from 'react'

export type TranslationFn = (key: string, defaultValue?: string) => string

export type CommerceComponentsContextType = {
  t: TranslationFn
}

export const CommerceComponentsContext = React.createContext<CommerceComponentsContextType>({
  t: (key, defaultValue) => defaultValue || key,
})

type CommerceComponentsProviderProps = CommerceComponentsContextType & { children: any }

export const CommerceComponentsProvider: React.FunctionComponent<CommerceComponentsProviderProps> = ({
  t,
  children,
}: CommerceComponentsProviderProps) => {
  return (
    <CommerceComponentsContext.Provider value={{ t }}>
      {children}
    </CommerceComponentsContext.Provider>
  )
}
