import classnames from 'classnames'
import React, { useState } from 'react'

import { useTranslation } from '@hooks/use-translation'

import { CountrySelect } from './country-select'
import { StateSelect } from './state-select'
import { AddressFormField, AddressType } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  onSubmit: (value: object) => boolean
  children?: any
  formConfig?: AddressFormField[]
  styles?: any
  fill?: AddressType
  className?: string
}

const defaultFormConfig = [
  {
    name: 'first_name',
    label: 'First Name',
    required: true,
  },
  {
    name: 'last_name',
    label: 'Last Name',
    required: true,
  },
  {
    name: 'company',
    label: 'Company',
  },
  {
    name: 'address1',
    label: 'Address Line 1',
    required: true,
  },
  {
    name: 'address2',
    label: 'Address Line 2',
  },
  {
    name: 'city',
    label: 'City',
    required: true,
  },
  {
    name: 'postal_code',
    label: 'Postal Code',
    required: true,
  },
  {
    name: 'state_or_province',
    label: 'State',
    required: true,
    component: StateSelect,
  },
  {
    name: 'country_code',
    label: 'Country',
    required: true,
    component: CountrySelect,
  },
  {
    name: 'phone',
    label: 'Phone',
  },
]

export const getShippingFormConfig = (): AddressFormField[] => defaultFormConfig

const ShippingAddressForm: React.FunctionComponent<Props> = ({
  onSubmit,
  action,
  children = [],
  formConfig = defaultFormConfig,
  fill,
  styles = {},
  className = '',
}: Props) => {
  const { t } = useTranslation()
  const [address, setAddress] = useState(fill || {})

  const onFieldChange = (e: React.FormEvent<HTMLInputElement>): void => {
    const { name, value } = e.target as any
    const newAddress = { ...address }
    newAddress[name] = value
    setAddress(newAddress)
  }

  const fields = formConfig.map(config => {
    const fieldProps = {
      name: config.name,
      required: config.required,
      id: `address-form-${config.name}`,
      className: styles[config.name],
      value: address ? address[config.name] : '',
      key: `address_${config.name}`,
    }
    const FieldComponent = config.component
    const classes = [styles.fieldWrapper, styles[`${config.name}-wrapper`]] as any

    return (
      <div key={`field-${config.name}`} className={classes}>
        <label className={styles[`${config.name}-label`]} htmlFor={`address-form-${config.name}`}>
          {t(`bc.fields.${config.name}`, config.label)}
        </label>
        {FieldComponent ? (
          <FieldComponent address={address} onChange={onFieldChange} {...fieldProps} />
        ) : (
          <input onChange={onFieldChange} {...fieldProps} />
        )}
      </div>
    )
  })

  const submit = (e): boolean => {
    const formData = new FormData(e.target)
    const newAddress = {}
    formData.forEach((value, key) => {
      newAddress[key] = value
    })
    return onSubmit({ ...e, data: newAddress })
  }

  return (
    <form
      className={classnames(styles.addressForm, className)}
      action={action}
      method="POST"
      encType="multipart/form-data"
      onSubmit={submit}
    >
      {fields}
      {children}
      <button className={styles.saveButton} type="submit">
        {t('bc.address.save', 'Save Shipping Address')}
      </button>
    </form>
  )
}

export { ShippingAddressForm }
