/** @jsx jsx */
import { jsx } from '@emotion/react'
import { FunctionComponent } from 'react'
import { Clickable } from 'reakit/Clickable'
import { Role } from 'reakit/Role'

import { BannerPosition, useBannerStyle } from '@theme/banner'

export type BannerProps<T = React.HTMLProps<HTMLElement>> = T & {
  children: React.ReactNode
  onClose?: React.MouseEventHandler<HTMLButtonElement>
  position?: BannerPosition
  as?: React.ComponentType<T> | React.ElementType
}

export const Banner: FunctionComponent<BannerProps> = ({
  children,
  position = 'top',
  onClose,
  as = 'div',
  ...rest
}: BannerProps) => {
  const styles = useBannerStyle()

  return (
    <Role as={as} css={styles.Container?.(position)} {...rest}>
      {children}
      {onClose && (
        <Clickable onClick={onClose} css={styles.Close} aria-label="Close">
          &times;
        </Clickable>
      )}
    </Role>
  )
}
