import classnames from 'classnames'
import partial from 'lodash/partial'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { Wishlist } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  wishlist: Wishlist
  onWishlistAction: (action: string, wishlist: Wishlist) => void
  styles: any
  className?: string
}

const WishlistRow: React.FunctionComponent<Props> = ({
  wishlist,
  onWishlistAction = () => {},
  styles = {},
  className = '',
}: Props) => {
  const { t } = useTranslation()

  return (
    <div className={classnames(styles.wishlist, className)}>
      <div className={styles.wishlistInfo}>
        <a
          href="#"
          dangerouslySetInnerHTML={{ __html: wishlist.name }}
          className={styles.wishlistName}
          onClick={partial(onWishlistAction, 'click', wishlist)}
        />
        <span className={styles.wishlistItems}>{wishlist.items.length}</span>
        <span className={styles.wishlistShared}>
          {wishlist.is_public ? t('bc.btn.yes', 'Yes') : t('bc.btn.no', 'No')}
        </span>
      </div>
      <span className={styles.wishlistActions}>
        {wishlist.is_public && (
          <button
            className={styles.shareWishlistButton}
            type="button"
            onClick={partial(onWishlistAction, 'share', wishlist)}
          >
            {t('bc.btn.share', 'Share')}
          </button>
        )}
        <button
          className={styles.editWishlistButton}
          type="button"
          onClick={partial(onWishlistAction, 'edit', wishlist)}
        >
          {t('bc.btn.edit', 'Edit')}
        </button>
        <button
          className={styles.deleteWishlistButton}
          type="button"
          onClick={partial(onWishlistAction, 'delete', wishlist)}
        >
          {t('bc.btn.delete', 'Delete')}
        </button>
      </span>
    </div>
  )
}

export { WishlistRow, Props as WishlistRowProps }
