import React from 'react'

import { ProductDataFetchType, useProductData } from '../../hooks/product-detail-data'

interface Props extends React.HTMLProps<HTMLElement> {
  getProductData: ProductDataFetchType
  productId: string
  children: any
}

const ProductDetailProvider: React.FunctionComponent<Props> = ({
  getProductData = {},
  productId,
  children = [],
}) => {
  const data = useProductData(getProductData, productId)
  return React.Children.map(children, child => {
    return child ? React.cloneElement(child, { ...data } as any) : null
  })
}

export { ProductDetailProvider, Props as ProductDetailProviderProps }
