import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type BannerTheme = Partial<{
  Container: (position: BannerPosition) => CSS
  Close: CSS
}>

export type BannerPosition = 'top' | 'bottom'

export const Banner: BannerTheme = {
  Container: (position = 'top') => ({ typography, colors }) => css`
    ${typography['body-small'] as CSSPrimitive}
    padding: 12px 16px;
    color: ${colors['neutral-0']};
    background: ${colors['neutral-95']};
    text-align: center;
    position: sticky;
    width: 100%;
    left: 0;
    z-index: 100;
    ${position === 'top' ? `top: 0;` : `bottom: 0;`}
  `,
  Close: ({ colors }) => css`
    background: none;
    border: none;
    color: ${colors['neutral-0']};
    cursor: pointer;
    font-size: 32px;
    opacity: 0.5;
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 0 0 auto;
    padding: 0 5px;
    line-height: 1;

    &:hover {
      opacity: 0.75;
    }
  `,
}

export function useBannerStyle(): BannerTheme {
  const theme = useTheme()
  return theme?.components?.Banner
}
