import classnames from 'classnames'
import * as React from 'react'

import { LoginButton, LoginButtonProps } from './button'
import { ForgotLink, ForgotLinkProps } from './forgot-link'
import { Password, PasswordProps } from './password'
import { Remember, RememberProps } from './remember'
import { Username, UsernameProps } from './username'

interface Props extends React.HTMLProps<HTMLElement> {
  /**
   * onSubmit action call call. Value contains form values
   */
  onSubmit: (value: object) => boolean
  /**
   * form submit action
   */
  action: string
  /**
   * form submit action
   */
  children?: any
  /**
   * styles object
   */
  styles: any
  /**
   * form classname
   */
  className?: string
}

/**
 * LoginForm component - Form that wraps children and defaults to username/children
 */
const LoginForm: React.FunctionComponent<Props> = ({
  onSubmit,
  action,
  children = [],
  styles = {},
  className = '',
}: Props) => {
  const defaultChildren = [
    <Username />,
    <Password />,
    <Remember />,
    <LoginButton />,
    <ForgotLink link="/forgot-password" />,
  ]

  const submit = (e: any): boolean => {
    e.preventDefault()
    const formData = new FormData(e.target)
    const data = {}
    formData.forEach((value, key) => {
      data[key] = value
    })
    if (onSubmit) {
      return onSubmit({ ...e, data })
    }

    return true
  }

  const childs = children && children.length ? children : defaultChildren

  return (
    <form
      className={classnames(styles.loginForm, className)}
      action={action}
      method="POST"
      encType="multipart/form-data"
      onSubmit={submit}
    >
      {
        React.Children.map(childs, child =>
          child ? React.cloneElement(child, { styles } as any) : null
        ) as any
      }
    </form>
  )
}

export {
  LoginForm,
  Props as LoginFormProps,
  Username,
  Password,
  Remember,
  LoginButton,
  ForgotLink,
  UsernameProps,
  PasswordProps,
  RememberProps,
  LoginButtonProps,
  ForgotLinkProps,
}
