import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type ProductPriceTheme = Partial<{
  base: CSS
}>

export const ProductPrice: ProductPriceTheme = {
  base: theme => css`
    --gap: 12px;
    ${theme.typography['body-bold'] as CSSPrimitive}
    display: inline-flex;
    flex-wrap: wrap;
    margin: 0 0 0 calc(-1 * var(--gap));
    width: calc(100% + var(--gap));
    .ProductPrice__Item {
      margin: 0 0 0 var(--gap);
    }
    .ProductPrice__Item--old {
      text-decoration: line-through;
      ${theme.typography.body as CSSPrimitive}
    }
    .ProductPrice__Item--sale {
      color: ${theme.colors.attention};
    }
  `,
}

export function useProductPriceStyle(): CSS {
  const theme = useTheme()
  return theme?.components?.ProductPrice?.base
}
