import * as React from 'react'

interface Props extends React.HTMLProps<HTMLElement> {
  label?: string
  styles?: any
  className?: string
}

const Remember: React.FunctionComponent<Props> = ({
  label = 'Remember Me',
  styles = {},
  className = '',
}: Props) => {
  return (
    <div className={className}>
      <input
        key="remember-field"
        type="checkbox"
        name="remember"
        id="remember"
        className={styles.remember}
      />
      <label key="remember-label" htmlFor="remember" className={styles.rememberLabel}>
        {label}
      </label>
    </div>
  )
}

export { Remember, Props as RememberProps }
