/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'

import { useDropdownTheme } from '@theme/dropdown'

export type DropdownProps = React.SelectHTMLAttributes<HTMLSelectElement> & {
  label?: string
  name: string
  options: React.OptionHTMLAttributes<HTMLOptionElement>[]
}

export function Dropdown(props: DropdownProps): React.ReactElement {
  const { label, name, options = [], ...select } = props
  const css = useDropdownTheme()

  return (
    <div css={css.Container}>
      {label && (
        <label css={css.Label} htmlFor={`input-${name}`}>
          {label}
        </label>
      )}
      <div css={css.Wrapper}>
        <select aria-labelledby={`input-${name}`} name={name} css={css.Select} {...select}>
          {select.placeholder && (
            <option value="" disabled selected hidden>
              {select.placeholder}
            </option>
          )}
          {options.map(option => (
            <option css={css.Option} key={option.label} {...option}>
              {option.label}
            </option>
          ))}
        </select>
        <span className="focus" />
      </div>
    </div>
  )
}
