import classnames from 'classnames'
import * as React from 'react'

interface Props extends React.HTMLProps<HTMLElement> {
  styles?: any
  children?: any
  className?: string
  link: string
}

const ForgotLink: React.FunctionComponent<Props> = ({
  children = ['Forgot your password?'],
  styles = {},
  link = '',
  className = '',
}: Props) => (
  <a
    href={link}
    rel="noreferrer"
    target="_blank"
    className={classnames(styles.forgotLink, className)}
  >
    {children}
  </a>
)

export { ForgotLink, Props as ForgotLinkProps }
