import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type StatusVariants = 'info' | 'warning' | 'success' | 'attention' | 'danger'

export type OrderRowTheme = {
  OrderRow: CSS
  Header: CSS
  OrderNumber: CSS
  OrderDetails: CSS
  OrderStatus: (variant?: StatusVariants) => CSS
  Products: CSS
  ProductImage: CSS
  ProductQuantity: CSS
  Product: CSS
  ProductText: CSS
  InfoRow: CSS
  OrderPrice: CSS
  OrderDetailsButton: CSS
}

export const OrderRow: OrderRowTheme = {
  OrderRow: ({ colors, mq }) => css`
    display: grid;
    column-gap: 48px;
    row-gap: 24px;
    border-bottom: 2px solid ${colors['neutral-15']};
    padding: 56px 0;

    &:first-of-type {
      border-top: 2px solid ${colors['neutral-15']};
    }

    ${mq[1]} {
      grid-template-columns: 250px auto 272px;
    }
  `,
  Header: css`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  `,
  OrderNumber: ({ typography }) => css`
    ${typography['display-xx-small'] as CSSPrimitive}
    text-transform: uppercase;
  `,
  OrderDetails: () => css`
    padding: 0 32px;
    margin-top: 32px;

    @media (min-width: 960px) {
      margin-top: 0;
    }
  `,
  OrderStatus: (colorVariant = 'info') => ({ colors, typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    background: ${colors['neutral-10']};
    padding: 2px 16px 2px 32px;
    margin: 16px 0 21px;
    position: relative;
    border-radius: 46px;
    white-space: nowrap;

    &:before {
      content: '';
      border-radius: 100%;
      width: 8px;
      height: 8px;
      top: 10px;
      position: absolute;
      display: block;
      left: 16px;
      background: ${colors[colorVariant]};
    }
  `,
  Products: css``,
  Product: css`
    margin-bottom: 24px;
    display: flex;
    align-items: flex-start;
    column-gap: 24px;
  `,
  ProductQuantity: ({ typography, colors }) => css`
    ${typography['body-small'] as CSSPrimitive}
    color:${colors['neutral-55']};
    display: block;
    `,
  ProductImage: css`
    width: 80px;
  `,
  ProductText: css``,
  InfoRow: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  `,
  OrderPrice: css`
    font-weight: 700;
  `,
  OrderDetailsButton: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    background: none;
    border: none;
    padding: 0;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  `,
}

export function useOrderRowStyle(): OrderRowTheme {
  const theme = useTheme()
  return theme?.components?.OrderRow
}
