/**
 * @function CartProvider
 *
 */

import classnames from 'classnames'
import React from 'react'

import { useTranslation } from '@hooks/use-translation'

import { CartItems } from '../cart-items'
import { Checkout } from '../checkout'
import { Subtotal } from '../subtotal'
import { CartType } from '../types'
import { cartHasItems } from '../utilities'

interface Props extends React.HTMLProps<HTMLElement> {
  cart: CartType
  children: any
  styles?: any
  onUpdate: (method: string, id: string, payload: object) => void
  onCheckout?: (value: object) => boolean
  className?: string
}

const Provider: React.FunctionComponent<Props> = ({
  cart,
  styles = {},
  // @ts-ignore
  children = [<CartItems />, <Subtotal />, <Checkout />],
  onUpdate = () => {},
  onCheckout = (): boolean => true,
  className = '',
}: Props) => {
  const { t } = useTranslation()

  if (!cartHasItems(cart)) {
    return (
      <div className={styles.emptyCart}>
        {t('bc.cart.empty', 'Please add some items to your cart.')}
      </div>
    )
  }

  return (
    <div className={classnames(styles.cartProvider, className)}>
      {
        React.Children.map(children, child =>
          child ? React.cloneElement(child, { cart, styles, onUpdate, onCheckout } as any) : null
        ) as any
      }
    </div>
  )
}

export { Provider, Props as ProviderProps }
