import { css, useTheme } from '@emotion/react'

import { CSS, CSSPrimitive } from './types'

export type DropdownTheme = Partial<{
  Container: CSS
  Wrapper: CSS
  Label: CSS
  Select: CSS
  Option: CSS
}>

export const Dropdown: DropdownTheme = {
  Wrapper: ({ colors }) => css`
    border: 2px solid ${colors['neutral-15']};
    border-radius: 24px;
    padding: 8px 20px;
    width: 100%;
    display: grid;
    grid-template-areas: 'select';
    align-items: center;
    cursor: pointer;
    position: relative;
    ::after {
      content: '';
      width: 0.8em;
      height: 0.5em;
      background-color: ${colors.neutral};
      clip-path: polygon(100% 0%, 0 0%, 50% 100%);
      grid-area: select;
      justify-self: end;
    }
    &:focus {
      .focus {
        position: absolute;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        border: 2px solid ${colors.neutral};
        border-radius: inherit;
      }
    }
  `,
  Select: css`
    appearance: none;
    background-color: transparent;
    border: none;
    padding: 0 1em 0 0;
    margin: 0;
    width: 100%;
    font-family: inherit;
    font-size: inherit;
    cursor: inherit;
    line-height: inherit;
    outline: none;
    grid-area: select;
    z-index: 1;
  `,
  Label: ({ typography }) => css`
    ${typography['body-small'] as CSSPrimitive}
    font-weight: 700;
    display: block;
    margin-bottom: 8px;
  `,
}

export function useDropdownTheme(): DropdownTheme {
  const theme = useTheme()
  return theme?.components?.Dropdown
}
