/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'

import { useProductCardStyle } from '../../../theme/product-card'
import { ProductImage, ProductPrice, ProductTitle } from '../../core/product'
import { Button, ButtonProps } from '../../utilities/button'

interface ProductObject {
  name: string
  condition: string
  price: number
  sale_price: number
}

interface ImageObject {
  url_standard: string
  meta: string
}

interface BrandObject {
  name: string
}

export type Props = React.HTMLProps<HTMLAnchorElement> & {
  product: ProductObject
  productUrl?: string
  image: ImageObject
  brand: BrandObject
  currencySettings: object
  buttons?: ButtonProps[]
  onAddToCard?: ButtonProps['onClick']
  unsafeButtons?: boolean
}

const ProductCard: React.FunctionComponent<Props> = (props: Props) => {
  const {
    product,
    productUrl,
    image,
    brand,
    currencySettings,
    children,
    buttons = [
      {
        onClick: props.onAddToCard,
        children: 'Add to cart',
      },
    ],
    ...rest
  } = props

  const css = useProductCardStyle()

  const safeButtons = buttons.map(button => ({
    ...button,
    onClick: (event: React.MouseEvent<any, MouseEvent>) => {
      event.preventDefault()
      button.onClick?.(event)
    },
  }))

  /**
   * The product card children component can be overwritten by adding your own children where ever you implement
   * this component.
   */
  // TODO: Allow `as` prop to allow different HTML tag, not only `a`
  // TODO: I wouldn't allow children in this component

  // TODO: Image and text should be common instead of product specific?
  return (
    <a css={css} {...rest} href={productUrl}>
      {children ?? (
        <React.Fragment>
          <ProductImage src={image.url_standard} alt={image.meta}>
            {buttons && (
              <div className="buttons">
                {safeButtons.map(button => (
                  <span className="button-container" key={button.id}>
                    <Button {...button} />
                  </span>
                ))}
              </div>
            )}
          </ProductImage>

          <ProductTitle text={product.name} as="p" className="product-title" />
          <ProductPrice
            price={product.price}
            salePrice={product.sale_price}
            currencySettings={currencySettings}
          />
        </React.Fragment>
      )}
    </a>
  )
}

export { ProductCard }
