import { css, useTheme } from '@emotion/react'

import { getBaseStyle } from '../utils'
import { CSS, CSSPrimitive } from './types'
import { typography } from './typography'

type PaginationComponents = 'Container' | 'Previous' | 'Next' | 'Ellipsis' | 'Page'

export type PaginationTheme = Partial<Record<PaginationComponents, { base: CSS }>>

export const Pagination: PaginationTheme = {
  Container: {
    base: css`
      display: inline-flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      --gap: 12px;
      margin: calc(-1 * var(--gap)) 0 0 calc(-1 * var(--gap));
      width: calc(100% + var(--gap));
      > * {
        margin: var(--gap) 0 0 var(--gap);
      }
    `,
  },
  Page: {
    base: theme => css`
      width: 40px;
      height: 40px;
      border-radius: 100%;
      border: none;
      background-color: ${theme.colors['neutral-10']};
      ${typography['body-small'] as CSSPrimitive}
      text-align: center;
      cursor: pointer;
      &[disabled],
      &[aria-disabled='true'] {
        opacity: 0.5;
      }
      &[aria-current='true'] {
        opacity: 1;
        background-color: transparent;
        font-weight: 700;
        color: ${theme.colors['neutral-95']};
      }
      &:not([disabled]),
      &:not([aria-disabled='true']) {
        &:hover {
          background-color: ${theme.colors['neutral-95']};
          color: ${theme.colors['neutral-0']};
        }
      }
    `,
  },
  Previous: {
    base: theme => css`
      ${getBaseStyle(theme.components.Pagination.Page?.base, theme)}
    `,
  },
  Next: {
    base: theme => css`
      ${getBaseStyle(theme.components.Pagination.Page?.base, theme)}
    `,
  },
  Ellipsis: {
    base: css`
      width: 40px;
      height: 40px;
      text-align: center;
    `,
  },
}

export function usePaginationStyle(): Partial<Record<PaginationComponents, CSS>> {
  const theme = useTheme()
  return Object.entries(theme?.components?.Pagination).reduce(
    (
      acc: Partial<Record<PaginationComponents, CSS>>,
      [key, value]: [PaginationComponents, { base: CSS }]
    ) => {
      return {
        ...acc,
        [key]: value?.base,
      }
    },
    {}
  )
}
