/** @jsx jsx */
import { jsx } from '@emotion/react'
import * as React from 'react'
import { Button } from 'reakit/Button'

import { useQuantitySelectorStyle } from '../../../theme/quantity-selector'

export interface QuantityProps extends React.HTMLProps<HTMLElement> {
  defaultQuantity?: number
  quantity?: number
  onChangeQuantity?: (quantity: number) => void
}

export function QuantitySelector(props: QuantityProps): React.ReactElement {
  const {
    onChangeQuantity = () => {},
    defaultQuantity = 0,
    quantity: overrideQuantity,
    ...rest
  } = props
  const [quantity, setQuantity] = React.useState(defaultQuantity)
  const firstRender = React.useRef(true)

  const css = useQuantitySelectorStyle()

  React.useEffect(() => {
    if (firstRender.current) {
      firstRender.current = false
      return
    }
    onChangeQuantity(quantity)
  }, [quantity])

  const onDecrease = (): void => setQuantity(currentQuantity => currentQuantity - 1)
  const onIncrease = (): void => setQuantity(currentQuantity => currentQuantity + 1)

  return (
    <span css={css} {...rest}>
      <Button className="QuantitySelector__Minus" onClick={onDecrease}>
        <svg width={12} height={2} viewBox="0 0 12 2" fill="none">
          <path d="M11 1H1" strokeWidth={2} strokeMiterlimit={10} strokeLinecap="square" />
        </svg>
      </Button>
      <span className="QuantitySelector__Value">{overrideQuantity ?? quantity}</span>
      <Button className="QuantitySelector__More" onClick={onIncrease}>
        <svg width={12} height={12} viewBox="0 0 12 12" fill="none">
          <path d="M6 1v10M11 6H1" strokeWidth={2} strokeMiterlimit={10} strokeLinecap="square" />
        </svg>
      </Button>
    </span>
  )
}
