import classnames from 'classnames'
import * as React from 'react'

import { AddressCard } from './address-card'
import { AddressType } from './types'

interface Props extends React.HTMLProps<HTMLElement> {
  addresses: AddressType[]
  AddressComponent?: any
  styles: any
  className?: string
  onDelete?: (address: AddressType) => void
  onEdit?: (address: AddressType) => void
}

const ShippingAddresses: React.FunctionComponent<Props> = ({
  addresses = [],
  styles = {},
  className = '',
  AddressComponent = AddressCard,
  onDelete,
  onEdit,
}: Props) => {
  return (
    <div className={classnames(styles.addresses, className)}>
      {addresses.map(address => (
        <AddressComponent
          key={address.id}
          address={address}
          styles={styles}
          onDelete={onDelete}
          onEdit={onEdit}
        />
      ))}
    </div>
  )
}

export { ShippingAddresses }
