/** @jsx jsx */
import { jsx } from '@emotion/react'
import cl from 'classnames'
import * as React from 'react'
import { Box, BoxProps } from 'reakit/Box'
import { Button as ReakitButton } from 'reakit/Button'
import { Role } from 'reakit/Role'
import { VisuallyHidden } from 'reakit/VisuallyHidden'

import { useHeroStyle } from '../../../theme/hero'
import { Button, ButtonProps } from '../../utilities/button'
import { Carousel } from './cerousel'

export type HeroProps = BoxProps & {
  headline?: React.HTMLProps<HTMLElement> & {
    text?: string
    as?: React.ElementType
  }
  description?: React.HTMLProps<HTMLElement> & {
    text?: string
    as?: React.ElementType
  }
  button?: ButtonProps
  image?: React.DetailedHTMLProps<React.ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement>
}

export type HeroArray = {
  slides: HeroProps[]
}

export function Hero(props: HeroArray): React.ReactElement {
  const { slides, ...rest } = props

  const cssHero = useHeroStyle()

  return (
    <Box {...rest} css={cssHero}>
      <Carousel
        renderArrowPrev={onClick => (
          <div className="hero-arrow hero-arrow--prev">
            <ReakitButton onClick={onClick}>
              <VisuallyHidden>Previous slide</VisuallyHidden>
              <svg width={8} height={12} viewBox="0 0 8 12" fill="none">
                <path
                  d="M6 10L2 6l4-4"
                  stroke="#8E8E8E"
                  strokeWidth={2}
                  strokeMiterlimit={10}
                  strokeLinecap="square"
                />
              </svg>
            </ReakitButton>
          </div>
        )}
        renderArrowNext={onClick => (
          <div className="hero-arrow hero-arrow--next">
            <ReakitButton onClick={onClick}>
              <VisuallyHidden>Next slide</VisuallyHidden>
              <svg width={8} height={12} viewBox="0 0 8 12" fill="none">
                <path
                  d="M6 10L2 6l4-4"
                  stroke="#8E8E8E"
                  strokeWidth={2}
                  strokeMiterlimit={10}
                  strokeLinecap="square"
                />
              </svg>
            </ReakitButton>
          </div>
        )}
        infiniteLoop
      >
        {slides.map(slide => (
          <React.Fragment key={slide.image?.alt}>
            <div className="hero-image">
              <img {...slide.image} alt={slide.image?.alt} />
            </div>
            <div className="hero-cta">
              {slide.headline && (
                <Role
                  as={slide.headline.as || 'h2'}
                  {...slide.headline}
                  className={cl('hero-headline', slide.headline.className)}
                >
                  {slide.headline.text ?? slide.headline.children}
                </Role>
              )}
              {slide.description && (
                <Role
                  as={slide.description.as || 'p'}
                  {...slide.description}
                  className={cl('hero-description', slide.description.className)}
                >
                  {slide.description.text ?? slide.description.children}
                </Role>
              )}
              {slide.button && (
                <Button {...slide.button} className={cl('hero-button', slide.button.className)} />
              )}
            </div>
          </React.Fragment>
        ))}
      </Carousel>
      {/*       <div className="hero-gallery">
        {images.map((image, index) => {
          // boo
          return (
            <div
              className="hero-image"
              css={css`
              left: 0;
              background-image: url('${image.original}');
              `}
            />
          )
        })}
      </div>
      <button onClick={decrease}>atras</button>
      <button onClick={increase}>adelante</button> */}
    </Box>
  )
}
